// axios imports
import { AuthenticatedHeaders } from "utils/ApiHeaders";

// backend route import
import { API_V2 } from "../Backend";

export default class PromoCodeAPI {
  public static validatePromoCode = async (data) => {
    try {
      const res = await fetch(`${API_V2}/promocode/validate`, {
        method: "POST",
        headers: AuthenticatedHeaders(),
        body: JSON.stringify(data),
      });
      return await res.json();
    } catch (err) {
      return { message: err.message };
    }
  };
}
