import { RAZERPAY_KEY } from "../../../../Backend";

import React, { useEffect, useState } from "react";
import { Collapse, Drawer, IconButton, Link } from "@mui/material";
import { ExpandMore, ExpandLess } from "@material-ui/icons";
import moment from "moment";
import CustomIcon from "../../../../shared-components/CustomIcon/CustomIcon";
import UIButton from "../../../../Components/UI/UIButton/UIButton";
import { useToastMessage } from "../../../../contexts";

import useStyles from "./styles/muiStyles";

import SubscriptionAPI from "../../../../api/Subscription/index";

import { useSubscription } from "../../../../contexts/SubscriptionProvider";
import { array } from "prop-types";

const tillDays = moment(new Date()).format("D");

const currentDate = moment(new Date()).format("Do MMM");
const date = new Date();
const lastDate = moment(
  new Date(date.getFullYear(), date.getMonth() + 1, 0)
).format("Do MMM");

const UpdateDrawer = ({ businessId, open, closeDrawer, onPaymentSuccess }) => {
  const classes = useStyles();
  const { setSuccessMessage, setErrorMessage } = useToastMessage();

  const {
    modifiedOrders,
    defaultOrders,
    updatedOrders,
    setNextMonthAmount,
    setInvoiceDetail,
    setPaymentStatus,
  } = useSubscription();

  const finalupdatedOrders = updatedOrders.filter(
    (order) => !order.forCancelled
  );

  const renewalAmount =
    finalupdatedOrders &&
    finalupdatedOrders.reduce(
      (total, currentValue) => (total = total + currentValue.amount),
      0
    );

  const nextRenewalAmount = renewalAmount * 1.18;

  setNextMonthAmount(nextRenewalAmount);

  const [totalAmount, setTotalAmount] = useState(0);
  const [taxableAmount, setTaxableAmount] = useState(0);
  const [gstAmt, setGstAmt] = useState(0);
  const [grandTotal, setGrandTotal] = useState(0);
  const [adjustableAmount, setAdjustableAmount] = useState(0);

  const [newPlanIds, setNewPlanIds] = useState([]);
  const [oldOrderIds, setOldOrderIds] = useState([]);

  const [remainMDay, setRemainMDay] = useState(() => {
    var date = new Date();
    var time = new Date(date.getTime());
    time.setMonth(date.getMonth() + 1);
    time.setDate(0);
    let remainD = time.getDate() - date.getDate() + 1;
    if (remainD === 0) {
      return 1;
    }
    return remainD;
  });

  const [daysInMonth, setDaysInMonth] = useState(() => {
    var now = new Date();
    return new Date(now.getFullYear(), now.getMonth() + 1, 0).getDate();
  });

  // console.log("defaultOrders", defaultOrders);
  let basic_Amt = 0;

  let remainAmount = 0;
  let updatedPlanIds = [];
  let oldOrderArr = [];

  console.log("modifiedOrders", modifiedOrders);

  for (var i = 0; i < modifiedOrders.length; i++) {
    basic_Amt += (modifiedOrders[i]["amount"] / daysInMonth) * remainMDay;
    let cat = modifiedOrders[i]["category"];
    updatedPlanIds.push(modifiedOrders[i]["planId"]);
    defaultOrders.map((defaultOrder) => {
      if (defaultOrder.plan_category === cat) {
        oldOrderArr.push(defaultOrder._id);
        remainAmount +=
          (defaultOrder.paid_amount / daysInMonth) * (remainMDay - 1);
      }
    });
  }

  const updateNewAndOldOrder = async () => {
    setNewPlanIds(updatedPlanIds);
    setOldOrderIds(oldOrderArr);
  };

  useEffect(() => {
    calculatePrice(basic_Amt, remainAmount);
    updateNewAndOldOrder();
  }, [basic_Amt, modifiedOrders]);

  const calculatePrice = async (basic_Amt, remainAmount) => {
    const taxableAmount = basic_Amt - remainAmount;
    let gstAmount = taxableAmount * 1.18 - taxableAmount;
    setGstAmt(gstAmount);
    setAdjustableAmount(remainAmount);
    setTaxableAmount(taxableAmount);
    setTotalAmount(basic_Amt);
    setGrandTotal(taxableAmount + gstAmount);
  };

  const openPayModal = async (amount) => {
    const res = await SubscriptionAPI.createSubscribePayment({
      businessId,
      amount,
    });
    if (res.error) {
      return setErrorMessage(res.message);
    }
    const cancelOrderIds = JSON.parse(
      sessionStorage.getItem("orderSubscribeCancelIds")
    );
    const options = {
      key: RAZERPAY_KEY,
      name: "Easy Social",
      description: "Subscription",
      order_id: res.id,
      handler: async (response) => {
        try {
          const paymentId = response.razorpay_payment_id;
          const captureResponse = await SubscriptionAPI.captureRazerpayOrder(
            paymentId,
            {
              businessId,
              amount,
              newPlanIds,
              oldOrderIds,
              adjustableAmount,
              cancelOrderIds,
            }
          );
          console.log("captureResponse", captureResponse);
          const paymentStatus = captureResponse.paymentData.status;
          const payableAmount = captureResponse.paymentData.amount / 100;

          if (paymentStatus === "captured") {
            setInvoiceDetail(captureResponse.data);
            closeDrawer();
            onPaymentSuccess();
            setPaymentStatus(true);
          } else {
            setErrorMessage("failed");
            setPaymentStatus(false);
          }
        } catch (err) {
          return setErrorMessage(err);
        }
      },
      theme: {
        color: "#686CFD",
      },
    };
    const rzp1 = new window.Razorpay(options);
    rzp1.open();
  };

  useEffect(() => {
    const script = document.createElement("script");
    script.src = "https://checkout.razorpay.com/v1/checkout.js";
    script.async = true;
    document.body.appendChild(script);
  }, []);

  const formatTwoPlace = (amount) => {
    return amount
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  return (
    <Drawer
      anchor={"right"}
      open={open}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") &&
        closeDrawer()
      }
      className={classes["update-subscriptions"]}
    >
      <div className="wrapper">
        <div className="header">
          <IconButton
            aria-label="Back"
            className="back-btn"
            style={{ color: "transparent", padding: 0 }}
            onClick={() => closeDrawer()}
            disableFocusRipple={true}
            disableRipple={true}
          >
            <CustomIcon.BackBtnIcon />
          </IconButton>
          <h1 className="title">{"Update Subscription"}</h1>
        </div>
        <div className="content">
          <div className="main">
            <h3>{"Order Summary"}</h3>
            <div className="alert-warning">
              <span className="icon">
                <svg
                  width="40"
                  height="35"
                  viewBox="0 0 40 35"
                  fill="none"
                  xmlns="//www.w3.org/2000/svg"
                >
                  <path
                    d="M21.6455 0.945865L39.7454 32.1622C39.9122 32.4498 40 32.776 40 33.1081C40 33.4402 39.9122 33.7664 39.7455 34.054C39.5787 34.3416 39.3388 34.5805 39.05 34.7465C38.7612 34.9126 38.4335 35 38.1 35H1.90001C1.56649 35 1.23884 34.9126 0.95 34.7465C0.66116 34.5805 0.421307 34.3416 0.254548 34.054C0.0877884 33.7664 -2.11825e-06 33.4402 0 33.1081C2.11832e-06 32.776 0.0877969 32.4498 0.25456 32.1622L18.3545 0.945865C18.5213 0.658283 18.7612 0.419475 19.05 0.253442C19.3388 0.0874085 19.6665 0 20 0C20.3335 0 20.6612 0.0874085 20.95 0.253442C21.2388 0.419475 21.4787 0.658283 21.6455 0.945865V0.945865ZM5.19092 31.2162H34.8091L20 5.67561L5.19092 31.2162V31.2162ZM18.0999 25.5405H21.9001V29.3243H18.0999V25.5405ZM18.0999 12.2972H21.9001V21.7567H18.0999V12.2972Z"
                    fill="#AB7070"
                  />
                </svg>
              </span>
              <div className="message">
                <h6>
                  {
                    "Billing cycle starts on 1st of every month. Pro-rata calculation applicable for current billing period."
                  }
                </h6>
              </div>
            </div>
            <div className="details">
              <div className="head">
                <h5 className="heading">{"Details"}</h5>
                <small className="duration">{`${currentDate} to ${lastDate}`}</small>
              </div>

              {modifiedOrders &&
                modifiedOrders.map((plan) => {
                  return (
                    <div className="particulars">
                      <p className="title">{`${plan.category} - ${plan.name} - ${remainMDay} Days`}</p>
                      <p className="amount">{`${formatTwoPlace(
                        (plan.amount / daysInMonth) * remainMDay
                      )}`}</p>
                    </div>
                  );
                })}

              <div className="particulars">
                <p className="title">{"Ajdustable Amount"}</p>
                <p className="amount">{`${formatTwoPlace(
                  adjustableAmount
                )}`}</p>
              </div>

              <div className="particulars">
                <p className="title">{"Taxable Amount"}</p>
                <p className="amount">{`${formatTwoPlace(taxableAmount)}`}</p>
              </div>
              <div className="particulars">
                <p className="title">{"GST Amount"}</p>
                <p className="amount">{`${formatTwoPlace(gstAmt)}`}</p>
              </div>
              <hr />
              <div className="particulars">
                <p className="title">
                  <strong>{"Total Payable Amount"}</strong>
                </p>
                <p className="amount">
                  <strong>{`${formatTwoPlace(grandTotal)}`}</strong>
                </p>
              </div>
            </div>
            <div className="box-wrapper">
              <h5 className="box-title">{"Next Renewal"}</h5>
              <div className="plan-box">
                <div className="desc">
                  <p>
                    <small>
                      {
                        "From now onwards your recurring billing value for this business is worth"
                      }
                    </small>
                    <strong>{`${formatTwoPlace(nextRenewalAmount)}`}</strong>
                    <small>{" inclusive all taxes."}</small>
                  </p>
                </div>
              </div>
            </div>
            <div className="actions">
              <UIButton
                type="button"
                variant="contained"
                /*                                  onClick={() => {
                  closeDrawer();
                  onPaymentSuccess();
                }}  */
                onClick={(e) => {
                  openPayModal(taxableAmount);
                }}
              >
                {"Pay & Update Subscription"}
              </UIButton>
            </div>
          </div>
        </div>
      </div>
    </Drawer>
  );
};

export default UpdateDrawer;
