/* eslint-disable react-hooks/exhaustive-deps */
import React, { useCallback, useEffect, useState } from "react";
import {
  Link,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";

import { useToastMessage } from "../../../../../contexts";
import SubscriptionAPI from "../../../../../api/Subscription/index";
import RenewDrawer from "../../RenewDrawer";
import PaymentDrawer from "../../PaymentDrawer";

const List = ({ className }) => {
  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  const [renew, toggleRenew] = useState(false);
  const [paid, togglePayment] = useState(false);
  const [subscriptions, setSubscriptions] = useState([]);
  const [businessId, setBusinessId] = useState(null);

  const loadSubscriptions = useCallback(async () => {
    const res = await SubscriptionAPI.getAllSubscriptions();
    if (res.error) {
      setErrorMessage(res.message);
    } else {
      setSubscriptions(res.data);
    }
  }, []);

  useEffect(() => {
    loadSubscriptions();
  }, [loadSubscriptions]);

  const formatTwoPlace = (amount) => {
    return amount
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  return (
    <div className={className}>
      <div className="card-group">
        <div className="card-header">
          <h3 className="card-title">{"Subscriptions"}</h3>
        </div>
        <div className="card-content">
          {subscriptions.length > 0 || true ? (
            <TableContainer>
              <Table aria-label="Subscriptions" className="transactions">
                <TableHead>
                  <TableRow>
                    <TableCell align="left">{"Business"}</TableCell>
                    <TableCell align="center" width={110}>
                      {"Renewal On"}
                    </TableCell>
                    <TableCell align="center">{"Amount"}</TableCell>
                    <TableCell align="center">{"Action"}</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {subscriptions.map((subscription, index) => {
                    let currency = subscription.orders[0]["currency"];
                    return (
                      <TableRow>
                        <TableCell align="left">
                          <p>
                            {subscription.business_name}
                            {subscription.orders.map((order) => {
                              return (
                                <>
                                  <small>{`${order.particulars} - ${order.short_desc}`}</small>
                                </>
                              );
                            })}
                          </p>
                        </TableCell>
                        <TableCell align="center">
                          {subscription.renewalDate}
                        </TableCell>
                        <TableCell align="center">
                          {" "}
                          {currency === "INR" ? "" : "$"}
                          {formatTwoPlace(subscription.totalAmount)}
                        </TableCell>
                        <TableCell align="center">
                          <div className="actions">
                            <Link
                              href={`/all-payments/subscriptions/${subscription.businessId}`}
                              underline="hover"
                            >
                              {"View"}
                            </Link>
                            {subscription.alreadyRenewalled === 0 && (
                              <Link
                                component="button"
                                underline="hover"
                                onClick={() => {
                                  toggleRenew(true);
                                  setBusinessId(subscription.businessId);
                                }}
                              >
                                {"Renew"}
                              </Link>
                            )}
                          </div>
                        </TableCell>
                      </TableRow>
                    );
                  })}
                </TableBody>
              </Table>
            </TableContainer>
          ) : (
            "No subscription available."
          )}
        </div>
      </div>
      {renew && (
        <RenewDrawer
          open={renew}
          businessId={businessId}
          closeDrawer={() => toggleRenew(false)}
          onPaymentSuccess={() => togglePayment(true)}
        />
      )}
      <PaymentDrawer
        businessId={businessId}
        open={paid}
        closeDrawer={() => togglePayment(false)}
      />
    </div>
  );
};

export default List;
