import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "modify-subscriptions": {
    "& .MuiBackdrop-root": {
      backgroundColor: "#00000080",
    },

    "& .wrapper": {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      width: "360px",

      "& .header": {
        alignItems: "center",
        borderBottom: "1px solid #24272C1A",
        display: "flex",
        flex: "0 1 0",
        gap: "10px",
        padding: "22px 30px",
        width: "100%",

        "& .back-btn": {
          "&:hover": {
            backgroundColor: "transparent",
          },
        },

        "& .title": {
          color: "#24272c",
          flexGrow: 1,
          fontWeight: 500,
          fontSize: "24px",
          lineHeight: "28px",
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        },
      },

      "& .content": {
        display: "flex",
        flex: "1 0 0",
        flexDirection: "column",
        overflowY: "auto",
        padding: "20px 30px",

        "& .main": {
          alignItems: "flex-start",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          rowGap: "30px",

          "& .payment-wrapper": {
            display: "flex",
            flexDirection: "column",
            rowGap: "80px",
            width: "100%",

            "& .status": {
              alignItems: "center",
              display: "flex",
              flexDirection: "column",
              rowGap: "10px",

              "& .success, & .failed": {
                display: "inline-flex",
              },

              "& .label": {
                color: "#24272C",
                fontSize: "24px",
                fontWeight: 500,
                lineHeight: "34px",
              },

              "& .amount": {
                color: "#566171",
                fontSize: "15px",
                fontWeight: 400,
                lineHeight: "22px",
              },
            },

            "& .next-payment": {
              display: "flex",
              flexDirection: "column",

              "& .title": {
                color: "#24272C",
                fontSize: "18px",
                fontWeight: 500,
                lineHeight: "21px",
                marginBottom: "16px",
              },

              "& .info": {
                alignItems: "center",
                borderBottom: "1px solid #24272C26",
                display: "flex",
                justifyContent: "space-between",
                padding: "10px 0",

                "& .label, & .value": {
                  color: "#566171",
                  fontSize: "15px",
                  fontWeight: 400,
                  lineHeight: "18px",
                },
              },
            },
          },

          "& .actions": {
            columnGap: "10px",
            display: "flex",
            justifyContent: "flex-end",
            marginTop: "auto",
            padding: "20px 0",
            width: "100%",

            "& .MuiButton-root": {
              backgroundColor: "#12B0EE",
              borderRadius: "8px",
              boxShadow: "none",
              color: "#ffffff",
              height: "48px",
              margin: 0,
              minWidth: "84px",
              padding: "10px",
              textTransform: "capitalize",

              "&.btn-close": {
                backgroundColor: "#BEBEBE",
              },
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
