import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "renew-subscriptions": {
    "& .MuiBackdrop-root": {
      backgroundColor: "#00000080",
    },

    "& .wrapper": {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      width: "360px",

      "& .header": {
        alignItems: "center",
        borderBottom: "1px solid #24272C1A",
        display: "flex",
        flex: "0 1 0",
        gap: "10px",
        padding: "22px 30px",
        width: "100%",

        "& .back-btn": {
          "&:hover": {
            backgroundColor: "transparent",
          },
        },

        "& .title": {
          color: "#24272c",
          flexGrow: 1,
          fontWeight: 500,
          fontSize: "24px",
          lineHeight: "28px",
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        },
      },

      "& .content": {
        display: "flex",
        flex: "1 0 0",
        flexDirection: "column",
        overflowY: "auto",
        padding: "20px 30px",

        "& .main": {
          alignItems: "flex-start",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          rowGap: "30px",

          "& h3": {
            color: "#2E3239",
            fontWeight: 500,
            fontSize: "24px",
            lineHeight: "28px",
            marginBottom: "24px",
          },

          "& .details": {
            display: "flex",
            flexDirection: "column",
            width: "100%",

            "& .head": {
              alignItems: "center",
              columnGap: "5px",
              display: "flex",
              marginBottom: "23px",

              "& .heading": {
                color: "#24272C",
                fontSize: "18px",
                fontWeight: 500,
                lineHeight: "21px",
              },

              "& .duration": {
                color: "#24272C80",
                fontSize: "12px",
                fontWeight: 500,
                lineHeight: "14px",
              },
            },

            "& hr": {
              marginTop: "8px",
              marginBottom: "14px",
            },

            "& .particulars": {
              alignItems: "center",
              display: "flex",
              justifyContent: "space-between",
              marginBottom: "16px",

              "& p": {
                color: "#566171",
                fontSize: "15px",
                fontWeight: 400,
                lineHeight: "18px",
                marginBottom: 0,

                "&.amount": {
                  textAlign: "right",

                  "& strong": {
                    fontWeight: 500,
                  },

                  "& small": {
                    display: "block",
                  },
                },
              },

              "& .discount-box": {
                alignItems: "center",
                display: "flex",
                columnGap: "5px",

                "& .MuiLink-root": {
                  color: "#566171",
                  fontSize: "15px",
                  fontWeight: 400,
                  lineHeight: "18px",
                },
              },

              "& .discount-input": {
                background: "#F2F3F7",
                borderRadius: "4px",
                border: "none",
                boxShadow: "none",
                color: "#7B8AA2",
                height: "36px",
                maxWidth: "99px",
                outline: "none",
                padding: "0 12px",
              },

              "& .discount-value": {
                color: "#47C055",
                fontSize: "15px",
                fontWeight: 400,
                lineHeight: "24px",
              },

              "& .discount-card": {
                alignItems: "center",
                background: "#47C055",
                borderRadius: "4px",
                display: "flex",
                height: "26px",
                justifyContent: "space-between",
                padding: "0 10px",
                width: "100px",

                "& small": {
                  color: "#ffffff",
                  fontSize: "13px",
                  fontWeight: 400,
                  lineHeight: "15px",
                },

                "& button": {
                  background: "transparent",
                  margin: 0,
                  padding: 0,
                },
              },
            },
          },

          "& .box-wrapper": {
            backgroundColor: "#F6F9FB",
            borderRadius: "8px",
            display: "flex",
            flexDirection: "column",
            marginTop: "10px",
            marginBottom: "16px",
            padding: "16px",
            rowGap: "10px",
            width: "100%",

            "& .box-title": {
              color: "#7B8AA2",
              fontSize: "15px",
              fontWeight: 500,
              lineHeight: "18px",
            },

            "& .billing-header": {
              alignItems: "center",
              display: "flex",
              justifyContent: "space-between",

              "& .billing-title": {
                color: "#7B8AA2",
                fontSize: "15px",
                fontWeight: 500,
                lineHeight: "18px",
              },

              "& .edit-action": {
                background: "transparent",
                color: "#7B8AA2",
                cursor: "pointer",
                fontSize: "11px",
                fontWeight: 400,
                lineHeight: "13px",
                padding: 0,
                textDecoration: "underline",
              },
            },

            "& .billing-desc-wrapper": {
              "& .billing-desc": {
                color: "#7B8AA2",
                fontSize: "13px",
                fontWeight: 400,
                lineHeight: "15px",
              },
            },

            "& .plan-box": {
              margin: "8px 0",

              "& .header": {
                alignItems: "flex-end",
                borderBottom: "none",
                display: "flex",
                justifyContent: "space-between",
                padding: 0,

                "& .info": {
                  "& .plan-name": {
                    color: "#7B8AA2",
                    fontSize: "13px",
                    fontWeight: 400,
                    lineHeight: "15px",
                  },

                  "& .price": {
                    color: "#7B8AA2",
                    fontSize: "10px",
                    fontWeight: 400,
                    lineHeight: "12px",
                  },
                },

                "& .action": {
                  columnGap: "10px",
                  display: "flex",

                  "& button": {
                    alignItems: "center",
                    backgroundColor: "transparent",
                    color: "#7B8AA2",
                    cursor: "pointer",
                    fontSize: "11px",
                    fontWeight: 400,
                    lineHeight: "13px",
                    margin: 0,
                    padding: 0,
                    textDecoration: "underline",
                  },
                },
              },

              "& .data": {
                "& ul": {
                  borderTop: "1px solid rgba(36, 39, 44, 0.1)",
                  borderBottom: "1px solid rgba(36, 39, 44, 0.1)",
                  display: "flex",
                  flexDirection: "column",
                  marginTop: "10px",
                  padding: "14px 0 14px 20px",
                  listStyle: "none",

                  "& li": {
                    color: "#24272C",
                    display: "flex",
                    fontWeight: 400,
                    fontSize: "10px",
                    lineHeight: "10px",
                    marginBottom: "10px",
                    opacity: 0.7,
                    paddingLeft: "16px",
                    position: "relative",

                    "&::before": {
                      backgroundImage:
                        "url('data:image/svg+xml,%0A%3Csvg%20width%3D%2216%22%20height%3D%2214%22%20viewBox%3D%220%200%2016%2014%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20fill-rule%3D%22evenodd%22%20clip-rule%3D%22evenodd%22%20d%3D%22M15.0906%203.02981L5.44038%2013.2503L0.854492%207.82645L3.14538%205.88948L5.56378%208.74977L12.9093%200.970215L15.0906%203.02981Z%22%20fill%3D%22%2347C055%22%2F%3E%3C%2Fsvg%3E')",
                      backgroundSize: "9px 8px",
                      backgroundRepeat: "no-repeat",
                      backgroundPosition: "left center",
                      top: 0,
                      left: 0,
                      width: "9px",
                      height: "10px",
                      content: "''",
                      display: "block",
                      position: "absolute",
                    },
                  },
                },
              },
            },
          },

          "& .actions": {
            display: "flex",
            justifyContent: "flex-start",
            marginTop: "auto",
            padding: "20px 0",
            width: "100%",

            "& .MuiButton-root": {
              borderRadius: "8px",
              boxShadow: "none",
              height: "48px",
              justifyContent: "center",
              margin: 0,
              padding: "0 10px",
              textTransform: "capitalize",
              //width: "132px",

              "&.MuiButton-contained": {
                background: "#47C055",
                color: "#ffffff",
              },
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
