import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "modify-subscriptions": {
    "& .MuiBackdrop-root": {
      backgroundColor: "#00000080",
    },

    "& .wrapper": {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      width: "360px",

      "& .header": {
        alignItems: "center",
        borderBottom: "1px solid #24272C1A",
        display: "flex",
        flex: "0 1 0",
        gap: "10px",
        padding: "22px 30px",
        width: "100%",

        "& .back-btn": {
          "&:hover": {
            backgroundColor: "transparent",
          },
        },

        "& .title": {
          color: "#24272c",
          flexGrow: 1,
          fontWeight: 500,
          fontSize: "24px",
          lineHeight: "28px",
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        },
      },

      "& .content": {
        display: "flex",
        flex: "1 0 0",
        flexDirection: "column",
        overflowY: "auto",
        padding: "20px 30px",

        "& .main": {
          alignItems: "flex-start",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          rowGap: "30px",

          "& .info": {
            alignItems: "center",
            display: "flex",
            flexDirection: "column",
            rowGap: "14px",
            justifyContent: "center",

            "& .info-heading": {
              color: "#24272C",
              fontSize: "15px",
              fontWeight: 500,
              lineHeight: "18px",
            },
          },

          "& .field": {
            alignItems: "center",
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            rowGap: "16px",
            width: "100%",

            "& .MuiSelect-root": {
              "&:focus": {
                backgroundColor: "transparent",
                borderRadius: "8px",
              },
            },
          },

          "& .plan-details": {
            backgroundColor: "#F6F9FB",
            borderRadius: "8px",
            display: "flex",
            flexDirection: "column",
            rowGap: "10px",
            padding: "15px",
            width: "100%",

            "& .heading": {
              color: "#7B8AA2",
              fontSize: "15px",
              fontWeight: 500,
              lineHeight: "18x",
            },

            "& .plan": {
              "& .name": {
                color: "#7B8AA2",
                fontSize: "13px",
                fontWeight: 400,
                lineHeight: "15x",
                marginBottom: "3px",
              },

              "& .price": {
                color: "#7B8AA2",
                fontSize: "10px",
                fontWeight: 400,
                lineHeight: "12x",
              },
            },

            "& .features": {
              listStyle: "none",
              marginBottom: "5px",
              width: "100%",

              "& li": {
                color: "#24272CB3",
                fontSize: "10px",
                fontWeight: 500,
                lineHeight: "8px",
                paddingLeft: "24px",
                position: "relative",
                marginBottom: "10px",

                "&::before": {
                  content: '""',
                  backgroundImage:
                    'url("data:image/svg+xml,%0A%3Csvg%20width%3D%2216%22%20height%3D%2214%22%20viewBox%3D%220%200%2016%2014%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20fill-rule%3D%22evenodd%22%20clip-rule%3D%22evenodd%22%20d%3D%22M15.0906%203.02981L5.44038%2013.2503L0.854492%207.82645L3.14538%205.88948L5.56378%208.74977L12.9093%200.970215L15.0906%203.02981Z%22%20fill%3D%22%2347C055%22%2F%3E%3C%2Fsvg%3E")',
                  backgroundSize: "9px 8px",
                  backgroundRepeat: "no-repeat",
                  backgroundPosition: "left center",
                  display: "block",
                  height: "11px",
                  left: 0,
                  position: "absolute",
                  top: 0,
                  width: "9px",
                },

                "&.star": {
                  "&::before": {
                    backgroundImage:
                      'url("data:image/svg+xml,%0A%3Csvg%20width%3D%2214%22%20height%3D%2213%22%20viewBox%3D%220%200%2014%2013%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20d%3D%22M7%200L8.5716%204.83688H13.6574L9.5429%207.82624L11.1145%2012.6631L7%209.67376L2.8855%2012.6631L4.4571%207.82624L0.342604%204.83688H5.4284L7%200Z%22%20fill%3D%22%23F8D44D%22%2F%3E%3C%2Fsvg%3E")',
                    backgroundSize: "14px 13px",
                    width: "14px",
                  },
                },
              },
            },
          },

          "& .actions": {
            display: "flex",
            justifyContent: "end",
            marginTop: "auto",
            padding: "20px 0",
            width: "100%",

            "& .MuiButton-root": {
              borderRadius: "8px",
              boxShadow: "none",
              height: "40px",
              margin: 0,
              minWidth: "84px",
              padding: "10px",
              textTransform: "capitalize",

              "&.MuiButton-contained": {
                backgroundColor: "#12B0EE",
                color: "#ffffff",
              },
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
