/* eslint-disable react-hooks/exhaustive-deps */
import React, { useCallback, useEffect, useState } from "react";
import {
  Link,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";

import { useToastMessage } from "../../../../../contexts";
import SubscriptionAPI from "../../../../../api/Subscription/index";

import RenewDrawer from "../../RenewDrawer";

const List = () => {
  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  const [renew, toggleRenew] = useState(false);

  const [subscriptions, setSubscriptions] = useState([]);

  const formatTwoPlace = (amount) => {
    return amount
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  const loadSubscriptions = useCallback(async () => {
    const res = await SubscriptionAPI.getAllSubscriptions();
    if (res.error) {
      setErrorMessage(res.message);
    } else {
      setSubscriptions(res.data);
    }
  }, []);

  useEffect(() => {
    loadSubscriptions();
  }, [loadSubscriptions]);

  return (
    <div className="card-group">
      <div className="card-header">
        <h3 className="card-title">{"Subscriptions"}</h3>
      </div>
      <div className="card-content">
        {subscriptions.length > 0 || true ? (
          <TableContainer>
            <Table aria-label="Subscriptions" className="transactions">
              <TableHead>
                <TableRow>
                  <TableCell align="left">{"Business"}</TableCell>
                  <TableCell align="center" width={110}>
                    {"Renewal On"}
                  </TableCell>
                  <TableCell align="center">{"Amount"}</TableCell>
                  <TableCell align="center">{"Action"}</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {subscriptions.map((subscription, index) => {
                  let currency = subscription.orders[0]["currency"];
                  return (
                    <TableRow>
                      <TableCell align="left">
                        <p>
                          {subscription.business_name}
                          {subscription.orders.map((order) => {
                            return (
                              <>
                                <small>{`${order.particulars} - ${order.short_desc}`}</small>
                              </>
                            );
                          })}
                        </p>
                      </TableCell>
                      <TableCell align="center">
                        {subscription.renewalDate}
                      </TableCell>
                      <TableCell align="center">
                        {" "}
                        {currency === "INR" ? "" : "$"}
                        {formatTwoPlace(subscription.totalAmount)}
                      </TableCell>
                      <TableCell align="center">
                        <div className="actions">
                          {false && (
                            <Link
                              href={`/all-payments/subscriptions/${subscription.businessId}`}
                              underline="hover"
                            >
                              {"View"}
                            </Link>
                          )}
                          {false && (
                            <Link
                              component="button"
                              underline="hover"
                              onClick={() => toggleRenew(true)}
                            >
                              {"Renew"}
                            </Link>
                          )}
                        </div>
                      </TableCell>
                    </TableRow>
                  );
                })}

                {!false && (
                  <TableRow>
                    <TableCell align="left">
                      <p>
                        {"Honda 2 Wheelers"}
                        <small>{"Engage - WhatsApp Chatbot"}</small>
                        <small>{"Engage - Additional Tem Members (3)"}</small>
                        <small>{"Curate - Social Media Management"}</small>
                        <small>{"Curate - Additional Storage (15GB)"}</small>
                      </p>
                    </TableCell>
                    <TableCell align="center">{"1 Feb 2022"}</TableCell>
                    <TableCell align="center">{" 4000.00"}</TableCell>
                    <TableCell align="center">
                      <div className="actions">
                        <Link
                          href="/all-payments/subscriptions/8943r82143138322"
                          underline="hover"
                        >
                          {"View"}
                        </Link>
                        <Link
                          component="button"
                          underline="hover"
                          onClick={() => toggleRenew(true)}
                        >
                          {"Renew"}
                        </Link>
                      </div>
                    </TableCell>
                  </TableRow>
                )}
                {!false && (
                  <TableRow>
                    <TableCell align="left">
                      <p>
                        {"Easy Social"}
                        <small>{"Engage - WhatsApp Chatbot"}</small>
                        <small>{"Engage - Additional Tem Members (3)"}</small>
                        <small>{"Curate - Social Media Management"}</small>
                        <small>{"Curate - Additional Storage (15GB)"}</small>
                      </p>
                    </TableCell>
                    <TableCell align="center">{"1 Feb 2022"}</TableCell>
                    <TableCell align="center">{" 2000.00"}</TableCell>
                    <TableCell align="center">
                      <div className="actions">
                        <Link
                          href="/all-payments/subscriptions/8943r82143138322"
                          underline="hover"
                        >
                          {"View"}
                        </Link>
                        <Link
                          component="button"
                          underline="hover"
                          onClick={() => toggleRenew(true)}
                        >
                          {"Renew"}
                        </Link>
                      </div>
                    </TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          </TableContainer>
        ) : (
          "No subscription available."
        )}
      </div>
      <RenewDrawer open={renew} closeDrawer={() => toggleRenew(false)} />
    </div>
  );
};

export default List;
