import React, { Fragment, useEffect, useState } from "react";

import {
  Button,
  Link,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";
import {
  DialogTitle,
  DialogContent,
  DialogActions,
  Dialog,
} from "@mui/material";

import { UIButton } from "../../../../../Components/exports";
import { useToastMessage } from "../../../../../contexts";
import { useSubscription } from "../../../../../contexts/SubscriptionProvider";

import SubscriptionAPI from "../../../../../api/Subscription/index";
import ModifyDrawer from "../../ModifyDrawer";
import AddServiceDrawer from "../../AddServiceDrawer";
import UpdateDrawer from "../../UpdateDrawer";
import PaymentDrawer from "../../PaymentDrawer";

const Details = ({ businessId, className }) => {
  const {
    defaultOrders,
    setDefaultOrders,
    updatedOrders,
    setUpdatedOrders,
    modifiedOrders,
    setModifiedOrders,
  } = useSubscription();

  const [doModify, toggleModify] = useState(false);
  const [addService, toggleAddService] = useState(false);
  const [update, toggleUpdate] = useState(false);
  const [paid, togglePayment] = useState(false);
  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  const [busiSubscription, setBusiSubscription] = useState([]);
  const [cancelAll, setCancelAll] = useState(false);
  //order id for cancell subscription
  const [orderCancelId, setOrderCancelId] = useState(null);

  //dialog state for delete
  const [openConfirmDialog, setOpenConfirmDialog] = useState(false);

  const [orderIndex, setOrderIndex] = useState();

  const loadSubscription = async () => {
    const res = await SubscriptionAPI.getBusiSubscription(businessId);
    if (res.error) {
      setErrorMessage(res.message);
    } else {
      // const orders = res.data.orders;
      setBusiSubscription(res.data);
      setDefaultOrders(res.data.orders);
      setUpdatedOrders(res.data.orders);
      setCancelAll(res.data.allCancelled);
    }
  };

  useEffect(() => {
    sessionStorage.setItem("orderSubscribeCancelIds", JSON.stringify([]));
    loadSubscription();
  }, []);

  //function for opening confirm box for delete
  const handleClickOpen = () => {
    setOpenConfirmDialog(true);
  };

  //function for closing confirm box for delete
  const handleClickClose = () => {
    setOpenConfirmDialog(false);
  };

  //function cancel subscription
  const cancelAllSubscription = async () => {
    const res = await SubscriptionAPI.subscriptionCancelled(businessId, {
      orderId: "all",
    });
    if (res.error) {
      setErrorMessage(res.message);
    } else {
      setSuccessMessage(res.message);
      setCancelAll(true);
      setTimeout(
        () => (window.location.href = "/all-payments/subscriptions"),
        1000
      );
    }
  };

  const cancelOrderSubscribe = async () => {
    const res = await SubscriptionAPI.subscriptionCancelled(businessId, {
      orderId: JSON.parse(sessionStorage.getItem("orderSubscribeCancelIds")),
    });
    if (res.error) {
      setErrorMessage(res.message);
    } else {
      setSuccessMessage(res.message);
      setTimeout(
        () => (window.location.href = "/all-payments/subscriptions"),
        1000
      );
    }
  };

  const resetAllChange = async () => {
    setUpdatedOrders(defaultOrders);
    setModifiedOrders([]);
    sessionStorage.setItem("orderSubscribeCancelIds", JSON.stringify([]));

    console.log("updated modify", modifiedOrders);
  };

  const cancelOrderSubscription = async () => {
    var storedOrderIds = JSON.parse(
      sessionStorage.getItem("orderSubscribeCancelIds")
    ); //get them back
    if (!storedOrderIds.includes(orderCancelId)) {
      storedOrderIds.push(orderCancelId);
      sessionStorage.setItem(
        "orderSubscribeCancelIds",
        JSON.stringify(storedOrderIds)
      );
      const newOrder = updatedOrders.map((order) => {
        if (order._id === orderCancelId) {
          let newOrder = { ...order };
          newOrder.forCancelled = true;
          return newOrder;
        } else {
          return order;
        }
      });
      setUpdatedOrders(newOrder);
      const newModify = modifiedOrders.filter(
        (modifyOrder) => modifyOrder.defaultOrderId !== orderCancelId
      );
      console.log("newModify", newModify);
      setModifiedOrders(newModify);
    }
  };

  const formatAmount = (amount) => {
    let amt = Math.round(amount);
    return amt
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  const formatTwoPlace = (amount) => {
    return amount
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  const setNewService = async (detail) => {
    const newPlan = {
      amount: detail.amount,
      currency: detail.currency,
      particulars: detail.category,
      plan_category: detail.category,
      plan_id: detail.planId,
      qty: 1,
    };
    setUpdatedOrders([...updatedOrders, newPlan]);
    setModifiedOrders([...modifiedOrders, detail]);

    const updatedAmount = busiSubscription.totalAmount + detail.amount;
    setBusiSubscription({ ...busiSubscription, totalAmount: updatedAmount });
  };

  const PlanItem = ({
    orderKey,
    plan_id,
    plan_category,
    particulars,
    short_desc,
    qty,
    amount,
    _id,
    isCancelled,
    forCancelled,
  }) => {
    const [editPlanItem, toggleEditPlan] = useState(false);

    const setNewPlan = async (detail) => {
      let default_orders = [...defaultOrders];
      let default_element = { ...default_orders[orderIndex] };
      let defaultPlanId = parseInt(default_element.plan_id);
      let defaultOrderId = default_element._id;
      // updated plan
      let temp_state = [...updatedOrders];
      let temp_element = { ...temp_state[orderIndex] };
      let oldAmount = temp_element.amount;
      let oldPlanId = parseInt(temp_element.plan_id);
      if (defaultPlanId != parseInt(detail.planId)) {
        if (parseInt(detail.planId) != oldPlanId) {
          const removeSameCat = modifiedOrders.filter(
            (modifiedOrder) => modifiedOrder.category != detail.category
          );
          let selectedPlan = { ...detail };
          selectedPlan.defaultOrderId = defaultOrderId;
          const newModifiedOrders = [...removeSameCat, selectedPlan];
          setModifiedOrders(newModifiedOrders);
        }
      } else {
        //const new modifiedOrders.filter(modified => modified.planId!==defaultPlanId);
        //setModifiedOrders(newModifiedOrders);
      }

      temp_element.amount = detail.amount;
      temp_element.plan_id = detail.planId;
      temp_state[orderIndex] = temp_element;
      setUpdatedOrders(temp_state);
      const updatedAmount =
        busiSubscription.totalAmount - oldAmount + detail.amount;
      setBusiSubscription({ ...busiSubscription, totalAmount: updatedAmount });
    };

    const restoreCancelledOrder = async (id) => {
      const canceledOrderIds = JSON.parse(
        sessionStorage.getItem("orderSubscribeCancelIds")
      );
      var newArray = canceledOrderIds.filter(function (f) {
        return f !== id;
      });
      sessionStorage.setItem(
        "orderSubscribeCancelIds",
        JSON.stringify(newArray)
      );

      const newOrder = updatedOrders.map((order) => {
        if (order._id === id) {
          let newOrder = { ...order };
          newOrder.forCancelled = false;
          return newOrder;
        } else {
          return order;
        }
      });
      setUpdatedOrders(newOrder);
    };

    return (
      <Fragment>
        <TableRow>
          <TableCell align="left">
            <div className="particular">
              <p style={{ color: isCancelled || forCancelled ? "red" : "" }}>
                {particulars} - {short_desc}
                {forCancelled && (
                  <>
                    <br />
                    <span>Marked for cancellation</span>
                  </>
                )}
              </p>
              {doModify && (
                <div className="actions">
                  {!isCancelled && !forCancelled && (
                    <Link
                      component="button"
                      underline="hover"
                      onClick={() => {
                        setOrderIndex(orderKey);
                        toggleEditPlan(true);
                      }}
                    >
                      {"Edit"}
                    </Link>
                  )}
                  {_id && !isCancelled && !forCancelled && (
                    <Link
                      component="button"
                      underline="hover"
                      onClick={() => {
                        handleClickOpen();
                        setOrderCancelId(_id);
                      }}
                    >
                      {"Cancel"}
                    </Link>
                  )}
                  {_id && forCancelled && (
                    <Link
                      component="button"
                      underline="hover"
                      onClick={() => {
                        restoreCancelledOrder(_id);
                      }}
                    >
                      {"Restore"}
                    </Link>
                  )}
                </div>
              )}
            </div>
          </TableCell>
          <TableCell align="center" style={{ color: isCancelled ? "red" : "" }}>
            {qty}
          </TableCell>
          <TableCell
            align="right"
            style={{ color: isCancelled || forCancelled ? "red" : "" }}
          >{` ${formatTwoPlace(amount)}`}</TableCell>
          <TableCell
            align="right"
            style={{ color: isCancelled || forCancelled ? "red" : "" }}
          >{` ${formatTwoPlace(amount * qty)}`}</TableCell>
        </TableRow>
        {editPlanItem && (
          <ModifyDrawer
            open={editPlanItem}
            closeDrawer={() => toggleEditPlan(false)}
            {...{
              orderKey,
              plan_id,
              plan_category,
              particulars,
              short_desc,
              qty,
              amount,
            }}
            setNewPlan={(detail) => setNewPlan(detail)}
          />
        )}

        <Dialog
          open={openConfirmDialog}
          aria-labelledby="responsive-dialog-title"
        >
          <DialogTitle id="responsive-dialog-title">
            {"Subscription Cancel"}
          </DialogTitle>
          <DialogContent>
            {"Are you sure you want to cancel subscription?"}
          </DialogContent>
          <DialogActions>
            <Button
              onClick={() => {
                handleClickClose();
              }}
            >
              {"Cancel"}
            </Button>
            {orderCancelId === "all" ? (
              <Button
                onClick={async () => {
                  await cancelAllSubscription();
                  handleClickClose();
                }}
                autoFocus
              >
                {"Submit"}
              </Button>
            ) : (
              <Button
                onClick={async () => {
                  await cancelOrderSubscription();
                  handleClickClose();
                }}
                autoFocus
              >
                {"Yes"}
              </Button>
            )}
          </DialogActions>
        </Dialog>
      </Fragment>
    );
  };

  return (
    <div className={className}>
      <div className="card-group">
        <div className="card-header">
          <Link
            className="card-title faded"
            href="/all-payments/subscriptions/"
            underline="none"
          >
            {"Subscriptions"}
          </Link>
          <span className="card-title faded">{" / "}</span>
          <span className="card-title">{busiSubscription.business_name}</span>
        </div>
        <div className="card-content">
          <div className="detail-header">
            <div className="heading">
              <h4>{busiSubscription.business_name}</h4>
            </div>
            <div className="activation-date">
              <strong>{"Activated On"}</strong>
              <small>{busiSubscription.renewalDate}</small>
            </div>
          </div>
          {doModify && defaultOrders.length !== 2 && (
            <div className="header-action">
              <Button
                variant="contained"
                onClick={() => toggleAddService(true)}
              >
                {"Add Service"}
              </Button>
            </div>
          )}
          <TableContainer>
            <Table aria-label="Subscriptions" className="transactions-detail">
              <TableHead>
                <TableRow>
                  <TableCell align="left">{"Particulars"}</TableCell>
                  <TableCell align="center" width={30}>
                    {"Unit"}
                  </TableCell>
                  <TableCell align="right" width={90}>
                    {"Amount"}
                  </TableCell>
                  <TableCell align="right" width={90}>
                    {"Amount"}
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {updatedOrders &&
                  updatedOrders.map((order, index) => (
                    <PlanItem orderKey={index} {...order} />
                  ))}
                <TableRow sx={{ borderTop: 1, borderColor: "#24272C1A" }}>
                  <TableCell
                    colSpan={3}
                    sx={{ border: 0, py: "10px", px: "5px" }}
                  >
                    <strong>{"Total"}</strong>
                  </TableCell>
                  <TableCell
                    align="right"
                    sx={{ border: 0, py: "10px", px: "5px" }}
                  >
                    <strong>{` ${formatAmount(
                      busiSubscription.totalAmount
                    )}`}</strong>
                  </TableCell>
                </TableRow>
                <TableRow sx={{ borderBottom: 1, borderColor: "#24272C1A" }}>
                  <TableCell
                    colSpan={3}
                    sx={{ border: 0, py: "10px", px: "5px" }}
                  >
                    <span>{`Taxes`}</span>
                  </TableCell>
                  <TableCell
                    align="right"
                    sx={{ border: 0, py: "10px", px: "5px" }}
                  >
                    <span>{` ${formatTwoPlace(
                      (busiSubscription.gst_rate / 100) *
                        busiSubscription.totalAmount
                    )}`}</span>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell
                    colSpan={3}
                    sx={{ border: 0, py: "10px", px: "5px" }}
                  >
                    <strong>{"Total Payable"}</strong>
                  </TableCell>
                  <TableCell
                    align="right"
                    sx={{ border: 0, py: "10px", px: "5px" }}
                  >
                    <strong>{` ${formatTwoPlace(
                      (busiSubscription.gst_rate / 100) *
                        busiSubscription.totalAmount +
                        busiSubscription.totalAmount
                    )}`}</strong>
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
          <div className="detail-footer">
            {!doModify && !cancelAll && (
              <Button
                variant="contained"
                className="btn-sub"
                onClick={async () => {
                  handleClickOpen();
                  setOrderCancelId("all");
                }}
              >
                {"Cancel"}
              </Button>
            )}
            {!doModify && !cancelAll && (
              <Button
                variant="contained"
                className="btn-main"
                onClick={() => toggleModify(true)}
              >
                {"Modify"}
              </Button>
            )}

            {doModify &&
              (modifiedOrders.length > 0 ||
                JSON.parse(sessionStorage.getItem("orderSubscribeCancelIds"))
                  .length > 0) && (
                <>
                  <Button
                    variant="contained"
                    className="btn-discard"
                    onClick={() => {
                      resetAllChange();
                      toggleModify(false);
                    }}
                  >
                    {"Discard"}
                  </Button>
                  <UIButton
                    onClick={() =>
                      modifiedOrders.length > 0
                        ? toggleUpdate(true)
                        : cancelOrderSubscribe()
                    }
                  >
                    {`Apply Changes (${
                      modifiedOrders.length +
                      JSON.parse(
                        sessionStorage.getItem("orderSubscribeCancelIds")
                      ).length
                    })`}
                  </UIButton>
                </>
              )}
          </div>
        </div>
      </div>
      {addService && (
        <AddServiceDrawer
          open={addService}
          closeDrawer={() => toggleAddService(false)}
          setNewPlan={(detail) => setNewService(detail)}
        />
      )}
      {update && (
        <UpdateDrawer
          businessId={businessId}
          open={update}
          closeDrawer={() => toggleUpdate(false)}
          onPaymentSuccess={() => togglePayment(true)}
        />
      )}
      <PaymentDrawer
        businessId={businessId}
        open={paid}
        closeDrawer={() => togglePayment(false)}
      />
    </div>
  );
};

export default Details;
