import React, { useState, useEffect } from "react";
import { Button, Divider, Drawer, IconButton, MenuItem } from "@mui/material";

import { CustomTextFieldFilled } from "../../../../Components/UI/CustomTextField/CustomTextField";
import CustomIcon from "../../../../shared-components/CustomIcon/CustomIcon";

import useStyles from "./styles/muiStyles";
import { useToastMessage } from "../../../../contexts";
import { useSubscription } from "../../../../contexts/SubscriptionProvider";

import { getCategoryPlan } from "../../../../api/Plan";

const ModifyDrawer = ({
  open,
  closeDrawer,
  orderKey,
  plan_id,
  plan_category,
  particulars,
  short_desc,
  qty,
  amount,
  setNewPlan,
}) => {
  const classes = useStyles();
  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  const { defaultOrders } = useSubscription();
  const [plans, setPlans] = useState([]);
  const [selectedPlan, setSelectedPlan] = useState("");
  const [planId, setPlanId] = useState(plan_id);

  const [defaultPlanAmt, setDefaultPlanAmt] = useState();

  const category = plan_category;
  const plan_type = "Monthly";

  const getPlan = async () => {
    const res = await getCategoryPlan(category, plan_type);
    if (res.error) {
      setErrorMessage(res.error);
    } else {
      setPlans(res.data);
    }
  };

  useEffect(() => {
    getPlan();
  }, []);

  useEffect(() => {
    const selectedP = plans.filter((plan) => plan.planId === parseInt(planId));
    setSelectedPlan(selectedP[0]);
  }, [plans, planId]);

  useEffect(() => {
    const defaultPlan = defaultOrders.filter(
      (order) => order.plan_category === category
    );
    if (defaultPlan.length > 0) {
      const defaultAmt = defaultPlan[0]["amount"];
      setDefaultPlanAmt(defaultAmt);
    }
  }, [plans]);

  const inputChangedHandler = (e) => {
    setPlanId(e.target.value);
  };

  const updateSubscription = async () => {
    if (defaultPlanAmt > selectedPlan.amount) {
      setErrorMessage("only upgrade allow");
    } else {
      setNewPlan(selectedPlan);
    }
  };

  const formatTwoPlace = (amount) => {
    return amount
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  return (
    <Drawer
      anchor={"right"}
      open={open}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") &&
        closeDrawer()
      }
      className={classes["modify-subscriptions"]}
    >
      <div className="wrapper">
        <div className="header">
          <IconButton
            aria-label="Back"
            className="back-btn"
            style={{ color: "transparent", padding: 0 }}
            onClick={() => closeDrawer()}
            disableFocusRipple={true}
            disableRipple={true}
          >
            <CustomIcon.BackBtnIcon />
          </IconButton>
          <h1 className="title">{"Modify Subscriptions"} </h1>
        </div>
        <div className="content">
          <div className="main">
            <div className="info">
              <h6 className="info-heading">
                {particulars} - {short_desc}
              </h6>
            </div>
            <div className="field">
              <CustomTextFieldFilled
                select
                label="Plan"
                placeholder="Plan"
                variant="filled"
                type="select"
                inputProps={{ "aria-label": "Without label" }}
                fullWidth
                value={planId}
                onChange={(e) => inputChangedHandler(e)}
              >
                {/* {["Basic Plan", "Smart Plan", "Professional"].map(
                  (plan, index) => {
                    return (
                      <MenuItem key={index} value={plan}>
                        {plan}
                      </MenuItem>
                    );
                  }
                )} */}
                {plans &&
                  plans.map((plan, index) => {
                    return (
                      <MenuItem key={index} value={plan.planId}>
                        {plan.name}
                      </MenuItem>
                    );
                  })}
              </CustomTextFieldFilled>
            </div>
            <div className="plan-details">
              <h4 className="heading">{"Plan Details"}</h4>
              <div className="plan">
                {selectedPlan && (
                  <>
                    <h5 className="name">
                      {selectedPlan.category} - {selectedPlan.name}
                    </h5>
                    <p className="price">
                      {selectedPlan.currency === "INR" ? "" : "$"}{" "}
                      {formatTwoPlace(selectedPlan.amount)}/
                      {selectedPlan.plan_type}
                    </p>
                  </>
                )}
              </div>
              <Divider variant="fullWidth" style={{ color: "#24272c1a" }} />

              <ul className={`features`}>
                {selectedPlan &&
                  selectedPlan.plan_detail
                    .split("\n")
                    .map((feature, index) => <li key={index}>{feature}</li>)}
              </ul>
            </div>
            <div className="actions">
              <Button
                type="button"
                onClick={() => updateSubscription()}
                variant="contained"
              >
                {"Update Subscription"}
              </Button>
            </div>
          </div>
        </div>
      </div>
    </Drawer>
  );
};

export default ModifyDrawer;
