import { RAZERPAY_KEY } from "../../../../Backend";
import React, { useEffect, useState } from "react";
import moment from "moment";
import { Button, Collapse, Drawer, IconButton, Link } from "@mui/material";
import {
  DialogTitle,
  DialogContent,
  DialogActions,
  Dialog,
} from "@mui/material";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
import CustomIcon from "../../../../shared-components/CustomIcon/CustomIcon";
import UIButton from "../../../../Components/UI/UIButton/UIButton";
import useStyles from "./styles/muiStyles";
import { useToastMessage } from "../../../../contexts";
import { useSubscription } from "../../../../contexts/SubscriptionProvider";

import PromoCodeAPI from "../../../../api/PromoCode";
import SubscriptionAPI from "../../../../api/Subscription/index";

const RenewDrawer = ({ open, closeDrawer, onPaymentSuccess, businessId }) => {
  const classes = useStyles();
  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  const { setNextMonthAmount, setInvoiceDetail, setPaymentStatus } =
    useSubscription();
  const [totalAmount, setTotalAmount] = useState(0);
  const [discount, setDiscount] = useState("");
  const [gstAmt, setGstAmt] = useState(0);
  const [taxableAmount, setTaxableAmount] = useState(0);
  const [grandTotal, setGrandTotal] = useState(0);
  const [promoInput, setPromoInput] = useState("");
  const [promoCode, setPromoCode] = useState("");
  const [gstRate, setGstRate] = useState(0);
  const [plans, setPlans] = useState([]);
  const [renewalMonth, setRenewalMonth] = useState(null);
  const [startDate, setStartDate] = useState("");
  const [lastDate, setLastDate] = useState("");
  const [daysInMonth, setDaysInMonth] = useState(0);
  const [remainMDay, setRemainMDay] = useState(0);

  const [removePlanId, setRemovePlanId] = useState(null);
  /*   const [remainMDay, setRemainMDay] = useState(() => {
    var date = new Date();
    var time = new Date(date.getTime());
    time.setMonth(date.getMonth() + 1);
    time.setDate(0);
    let remainD = time.getDate() - date.getDate() + 1;
    if (remainD === 0) {
      return 1;
    }
    return remainD;
  }); */

  /*   const [daysInMonth, setDaysInMonth] = useState(() => {
    var now = new Date();
    return new Date(now.getFullYear(), now.getMonth() + 1, 0).getDate();
  }); */

  //const currentDate = moment(new Date()).format("Do MMM");

  const date = new Date();
  /* const lastDate = moment(
    new Date(date.getFullYear(), date.getMonth() + 1, 0)
  ).format("Do MMM"); */

  useEffect(() => {
    loadRenewalOrderDetail();
  }, [businessId]);

  const loadRenewalOrderDetail = async () => {
    const res = await SubscriptionAPI.renewalOrderSummary(businessId);
    if (res.error) {
      setErrorMessage(res.message);
    } else {
      setStartDate(res.startDate);
      const fromDate = new Date(res.startDate);
      const lastDate = new Date(
        fromDate.getFullYear(),
        fromDate.getMonth() + 1,
        0
      );
      setLastDate(lastDate);
      setGstRate(res.gst_rate);
      setPlans(res.plans);
      setRenewalMonth(res.renewalMonth);

      // var now = new Date();
      // number of day in month
      const daysInMonth = new Date(
        fromDate.getFullYear(),
        fromDate.getMonth() + 1,
        0
      ).getDate();
      setDaysInMonth(daysInMonth);

      //remain days
      var time = new Date(fromDate.getTime());
      time.setMonth(fromDate.getMonth() + 1);
      time.setDate(0);
      let remainD = time.getDate() - fromDate.getDate() + 1;
      let remainMDay = remainD === 0 ? 1 : remainD;
      setRemainMDay(remainMDay);
      calPlanPrice(res.plans, res.gst_rate, daysInMonth, remainMDay);
    }
  };

  const calPlanPrice = async (plans, gstRate, daysInMonth, remainMDay) => {
    let basic_Amt = 0;
    let planAmount = 0;
    for (var i = 0; i < plans.length; i++) {
      basic_Amt += (plans[i]["amount"] / daysInMonth) * remainMDay;
      planAmount += parseFloat(plans[i]["amount"]);
    }

    const nextRenewAmt = parseFloat((gstRate / 100) * planAmount) + planAmount;
    if (nextRenewAmt > 0) {
      setNextMonthAmount(nextRenewAmt);
    }
    calculatePrice(basic_Amt, discount, gstRate);
  };

  const validatePromo = async () => {
    let promoCode = promoInput;
    const res = await PromoCodeAPI.validatePromoCode({ promoCode });
    if (res.error) {
      setErrorMessage(res.message);
    } else {
      let discount;
      if (res.promo.type === "flat") {
        discount = res.promo.discount_amount;
      } else {
        discount = (res.promo.discount_amount / 100) * totalAmount;
      }
      setDiscount(discount);
      setPromoCode(promoCode);
      calculatePrice(totalAmount, discount, gstRate);
    }
    /* if (promoCode > 5) {
      return toastMessage.setErrorMessage("Invalid promo");
    } */
  };

  const removePlan = async () => {
    const updatedPlans = plans.filter((plan) => plan.planId !== removePlanId);
    setPlans(updatedPlans);
    calPlanPrice(updatedPlans, gstRate, daysInMonth, remainMDay);
  };

  const clearDiscountHandler = () => {
    setDiscount("");
    setPromoCode("");
    let discount = 0;
    calculatePrice(totalAmount, discount, gstRate);
  };

  const calculatePrice = async (totalAmt, discount, gstRate) => {
    let taxableAmount = totalAmt - discount;
    let gstAmt = (18 / 100) * taxableAmount;
    setGstAmt(gstAmt);
    setTaxableAmount(taxableAmount);
    setTotalAmount(totalAmt);
    setGrandTotal(taxableAmount + gstAmt);
  };

  //dialog state for delete
  const [openConfirmDialog, setOpenConfirmDialog] = React.useState(false);
  //function for opening confirm box for delete
  const handleClickOpen = () => {
    setOpenConfirmDialog(true);
  };

  //function for closing confirm box for delete
  const handleClickClose = () => {
    setOpenConfirmDialog(false);
  };

  const [show, setShow] = useState([false, false]);

  const showPlanDetail = (index1) => {
    let accordian = show.map((s, index) => {
      if (index === index1) {
        return (show[index] = true);
      } else {
        return (show[index] = false);
      }
    });
    setShow(accordian);
  };

  const openPayModal = async (taxableAmount) => {
    let planIds = [];
    for (const plan of plans) {
      planIds.push(plan.planId);
    }

    const res = await SubscriptionAPI.createSubscribePayment({
      businessId,
      amount: taxableAmount,
    });
    if (res.error) {
      return setErrorMessage(res.error);
    }
    const options = {
      key: RAZERPAY_KEY,
      name: "Easy Social",
      description: "Renewal",
      order_id: res.id,
      handler: async (response) => {
        try {
          const paymentId = response.razorpay_payment_id;
          const captureResponse = await SubscriptionAPI.renewalPayment(
            paymentId,
            {
              businessId,
              amount: taxableAmount,
              planIds,
              promoCode,
              discount,
              renewalMonth,
              startDate,
            }
          );
          const paymentStatus = captureResponse.paymentData.status;
          if (paymentStatus === "captured") {
            setInvoiceDetail(captureResponse.data.newInvoices);
            closeDrawer();
            onPaymentSuccess();
            setPaymentStatus(true);
          } else {
            setErrorMessage("failed");
            setPaymentStatus(false);
          }
        } catch (err) {
          return setErrorMessage(err);
        }
      },
      theme: {
        color: "#686CFD",
      },
    };
    const rzp1 = new window.Razorpay(options);
    rzp1.open();
  };

  useEffect(() => {
    const script = document.createElement("script");
    script.src = "https://checkout.razorpay.com/v1/checkout.js";
    script.async = true;
    document.body.appendChild(script);
  }, []);

  const formatAmount = (amount) => {
    let amt = Math.round(amount);
    return amt
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  const formatTwoPlace = (amount) => {
    return amount
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  return (
    <>
      <Drawer
        anchor={"right"}
        open={open}
        onClose={(ev, reason) =>
          !(reason === "backdropClick" || reason === "escapeKeyDown") &&
          closeDrawer()
        }
        className={classes["renew-subscriptions"]}
      >
        <div className="wrapper">
          <div className="header">
            <IconButton
              aria-label="Back"
              className="back-btn"
              style={{ color: "transparent", padding: 0 }}
              onClick={() => closeDrawer()}
              disableFocusRipple={true}
              disableRipple={true}
            >
              <CustomIcon.BackBtnIcon />
            </IconButton>
            <h1 className="title">{"Renewal"}</h1>
          </div>
          <div className="content">
            <div className="main">
              <h3>{"Order Summary"}</h3>

              <div className="details">
                <div className="head">
                  <h5 className="heading">{"Details"}</h5>
                  <small className="duration">{`${moment(startDate).format(
                    "Do MMM"
                  )} to ${moment(lastDate).format("Do MMM")}`}</small>
                </div>

                {plans &&
                  plans.map((plan) => {
                    return (
                      <div className="particulars">
                        <p className="title">{`${plan.category} - ${plan.name} - ${remainMDay} Days`}</p>
                        <p className="amount">{`${formatTwoPlace(
                          (plan.amount / daysInMonth) * remainMDay
                        )}`}</p>
                      </div>
                    );
                  })}

                <div className="particulars">
                  <p className="title">{"Discount Promo"}</p>
                  <div className="discount-box">
                    {promoCode === "" ? (
                      <>
                        <input
                          type="text"
                          placeholder="Enter Code"
                          name="discount"
                          className="discount-input"
                          onChange={(e) => setPromoInput(e.target.value)}
                          value={promoInput}
                        />
                        <Link
                          component="button"
                          onClick={() => validatePromo()}
                          underline="none"
                        >
                          {"Check"}
                        </Link>
                      </>
                    ) : (
                      <span className="discount-value"> </span>
                    )}
                  </div>
                </div>
                <div className="particulars">
                  {promoCode !== "" && (
                    <div className={"discount-card"}>
                      <small>{String(promoCode).toUpperCase()}</small>
                      <button type="button" onClick={clearDiscountHandler}>
                        <svg
                          width="10"
                          height="10"
                          viewBox="0 0 10 10"
                          fill="none"
                          xmlns="//www.w3.org/2000/svg"
                        >
                          <g opacity="0.7">
                            <path d="M1 1L9.5 9.5" stroke="#ffffff" />
                            <path d="M9.5 1L1 9.5" stroke="#ffffff" />
                          </g>
                        </svg>
                      </button>
                    </div>
                  )}
                </div>
                <div className="particulars">
                  <p className="title">{"Taxable Amount"}</p>
                  <p className="amount">{`${formatTwoPlace(
                    taxableAmount
                  )}`}</p>
                </div>
                <div className="particulars">
                  <p className="title">{"GST Amount"}</p>
                  <p className="amount">{`${formatTwoPlace(gstAmt)}`}</p>
                </div>
                <hr />
                <div className="particulars">
                  <p className="title">
                    <strong>{"Total Payable Amount"}</strong>
                  </p>
                  <p className="amount">
                    <strong>{`${formatTwoPlace(grandTotal)}`}</strong>
                  </p>
                </div>
              </div>
              <div className="box-wrapper">
                <h5 className="box-title">{"Plan Detail"}</h5>
                {plans &&
                  plans.map((plan, index) => {
                    const price = formatAmount(plan.amount);
                    const planDetail = plan.plan_detail.split("\n");
                    return (
                      <div className="plan-box">
                        <div className="header">
                          <div className="info">
                            <h6 className="plan-name">
                              {plan.category} {plan.name}
                            </h6>

                            <small className="price">{`${price}/Month`}</small>
                          </div>
                          <div className="action">
                            {plans.length > 1 && (
                              <button
                                type="button"
                                onClick={() => {
                                  setRemovePlanId(plan.planId);
                                  handleClickOpen();
                                }}
                              >
                                Remove
                              </button>
                            )}
                            <button
                              onClick={() => showPlanDetail(index)}
                              type="button"
                            >
                              {"Details "}
                              {show[index] ? (
                                <ExpandLessIcon style={{ fontSize: "16px" }} />
                              ) : (
                                <ExpandMoreIcon style={{ fontSize: "16px" }} />
                              )}
                            </button>
                          </div>
                        </div>
                        <Collapse in={show[index]} className="data">
                          <ul>
                            {planDetail.map((feature, index) => (
                              <li
                                key={index}
                                className={feature.includes("*") ? "star" : ""}
                              >
                                {feature.includes("*")
                                  ? feature.slice(1)
                                  : feature}
                              </li>
                            ))}
                          </ul>
                        </Collapse>
                      </div>
                    );
                  })}
              </div>
              <div className="actions">
                <UIButton
                  type="button"
                  variant="contained"
                  /*                 onClick={() => {
                  closeDrawer();
                  onPaymentSuccess();
                }} */
                  onClick={(e) => {
                    openPayModal(Math.round(taxableAmount));
                  }}
                >
                  {`Pay ${formatAmount(grandTotal)}`}
                </UIButton>
              </div>
            </div>
          </div>
        </div>
      </Drawer>

      <Dialog
        open={openConfirmDialog}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">Delete</DialogTitle>
        <DialogContent>Are you sure you want to remove?</DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              handleClickClose();
            }}
          >
            Cancel
          </Button>
          <Button
            onClick={async () => {
              await removePlan();
              handleClickClose();
            }}
            autoFocus
          >
            Remove
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default RenewDrawer;
