import { API } from "../../Backend";
import encryptResponse from "../../Shared/encryptResponse";
import decryptResponse from "../../Shared/decryptResponse";

export default class SubscriptionAPI {
  public static getAllSubscriptions = async () => {
    try {
      const res = await fetch(`${API}/subscription-list`, {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          "X-Auth-Token": localStorage.getItem("token"),
          "X-BusinessId": localStorage.getItem("businessId"),
        },
      });
      return decryptResponse({ data: await res.json() });
    } catch (err) {
      return {
        error: err.message,
      };
    }
  };

  public static getBusiSubscription = async (businessId) => {
    try {
      const res = await fetch(`${API}/subscription/${businessId}`, {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          "X-Auth-Token": localStorage.getItem("token"),
          "X-BusinessId": localStorage.getItem("businessId"),
        },
      });
      return decryptResponse({ data: await res.json() });
    } catch (err) {
      return {
        error: err.message,
      };
    }
  };

  public static createSubscribePayment = async (data) => {
    try {
      const res = await fetch(`${API}/subscription/payment-request`, {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          "X-Auth-Token": localStorage.getItem("token"),
          "X-BusinessId": localStorage.getItem("businessId"),
        },
        body: JSON.stringify(data),
      });
      return decryptResponse({ data: await res.json() });
    } catch (err) {
      return { error: err.message };
    }
  };

  public static captureRazerpayOrder = async (paymentId: string, data) => {
    try {
      const res = await fetch(
        `${API}/subscription/capture-razerpay/${paymentId}`,
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            "X-Auth-Token": localStorage.getItem("token"),
            "X-BusinessId": localStorage.getItem("businessId"),
          },
          body: JSON.stringify(data),
        }
      );
      return decryptResponse({ data: await res.json() });
    } catch (err) {
      return { error: err.message };
    }
  };

  public static renewalPayment = async (paymentId: string, data) => {
    try {
      const res = await fetch(
        `${API}/subscription/renewal-payment/${paymentId}`,
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            "X-Auth-Token": localStorage.getItem("token"),
            "X-BusinessId": localStorage.getItem("businessId"),
          },
          body: JSON.stringify(data),
        }
      );
      return decryptResponse({ data: await res.json() });
    } catch (err) {
      return { error: err.message };
    }
  };

  public static subscriptionCancelled = async (businessId, orderId) => {
    try {
      const res = await fetch(`${API}/subscription/canclelled/${businessId}`, {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          "X-Auth-Token": localStorage.getItem("token"),
          "X-BusinessId": localStorage.getItem("businessId"),
        },
        body: JSON.stringify(orderId),
      });
      return decryptResponse({ data: await res.json() });
    } catch (err) {
      return {
        error: true,
        message: err.message,
      };
    }
  };

  public static renewalOrderSummary = async (businessId) => {
    try {
      const res = await fetch(
        `${API}/subscription/renewal-summary/${businessId}`,
        {
          method: "GET",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            "X-Auth-Token": localStorage.getItem("token"),
            "X-BusinessId": localStorage.getItem("businessId"),
          },
        }
      );
      return decryptResponse({ data: await res.json() });
    } catch (err) {
      return {
        error: true,
        message: err.message,
      };
    }
  };
}
