import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "update-subscriptions": {
    "& .MuiBackdrop-root": {
      backgroundColor: "#00000080",
    },

    "& .wrapper": {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      width: "360px",

      "& .header": {
        alignItems: "center",
        borderBottom: "1px solid #24272C1A",
        display: "flex",
        flex: "0 1 0",
        gap: "10px",
        padding: "22px 30px",
        width: "100%",

        "& .back-btn": {
          "&:hover": {
            backgroundColor: "transparent",
          },
        },

        "& .title": {
          color: "#24272c",
          flexGrow: 1,
          fontWeight: 500,
          fontSize: "24px",
          lineHeight: "28px",
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        },
      },

      "& .content": {
        display: "flex",
        flex: "1 0 0",
        flexDirection: "column",
        overflowY: "auto",
        padding: "20px 30px",

        "& .main": {
          alignItems: "flex-start",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",

          "& h3": {
            color: "#2E3239",
            fontWeight: 500,
            fontSize: "24px",
            lineHeight: "28px",
            marginBottom: "24px",
          },

          "& .alert-warning": {
            alignItems: "center",
            backgroundColor: "#FFF1E6",
            borderRadius: "8px",
            display: "flex",
            columnGap: "14px",
            padding: "14px",
            marginBottom: "24px",

            "& .message": {
              "& h6": {
                color: "#000000",
                fontSize: "12px",
                fontWeight: 400,
                lineHeight: "18px",
              },
            },
          },

          "& .details": {
            display: "flex",
            flexDirection: "column",
            width: "100%",

            "& .head": {
              alignItems: "center",
              columnGap: "5px",
              display: "flex",
              marginBottom: "23px",

              "& .heading": {
                color: "#24272C",
                fontSize: "18px",
                fontWeight: 500,
                lineHeight: "21px",
              },

              "& .duration": {
                color: "#24272C80",
                fontSize: "12px",
                fontWeight: 500,
                lineHeight: "14px",
              },
            },

            "& hr": {
              marginTop: "8px",
              marginBottom: "14px",
            },

            "& .particulars": {
              alignItems: "center",
              display: "flex",
              justifyContent: "space-between",
              marginBottom: "16px",

              "& p": {
                color: "#566171",
                fontSize: "15px",
                fontWeight: 400,
                lineHeight: "18px",
                marginBottom: 0,

                "&.amount": {
                  textAlign: "right",

                  "& strong": {
                    fontWeight: 500,
                  },

                  "& small": {
                    display: "block",
                  },
                },
              },

              "& .discount-box": {
                alignItems: "center",
                display: "flex",
                columnGap: "5px",

                "& .MuiLink-root": {
                  color: "#566171",
                  fontSize: "15px",
                  fontWeight: 400,
                  lineHeight: "18px",
                },
              },

              "& .discount-input": {
                background: "#F2F3F7",
                borderRadius: "4px",
                border: "none",
                boxShadow: "none",
                color: "#7B8AA2",
                height: "36px",
                maxWidth: "99px",
                outline: "none",
                padding: "0 12px",
              },

              "& .discount-value": {
                color: "#47C055",
                fontSize: "15px",
                fontWeight: 400,
                lineHeight: "24px",
              },

              "& .discount-card": {
                alignItems: "center",
                background: "#47C055",
                borderRadius: "4px",
                display: "flex",
                height: "26px",
                justifyContent: "space-between",
                padding: "0 10px",
                width: "100px",

                "& small": {
                  color: "#ffffff",
                  fontSize: "13px",
                  fontWeight: 400,
                  lineHeight: "15px",
                },

                "& button": {
                  background: "transparent",
                  margin: 0,
                  padding: 0,
                },
              },
            },
          },

          "& .box-wrapper": {
            backgroundColor: "#F6F9FB",
            borderRadius: "8px",
            display: "flex",
            flexDirection: "column",
            marginTop: "10px",
            marginBottom: "16px",
            padding: "16px",
            rowGap: "10px",
            width: "100%",

            "& .box-title": {
              color: "#7B8AA2",
              fontSize: "15px",
              fontWeight: 500,
              lineHeight: "18px",
            },

            "& .plan-box": {
              marginTop: "8px",

              "& .desc": {
                "& p": {
                  color: "#7B8AA2",

                  "& small": {
                    fontSize: "13px",
                    fontWeight: 400,
                    lineHeight: "15px",

                    "&:first-child": {
                      display: "block",
                    },
                  },

                  "& strong": {
                    fontSize: "15px",
                    fontWeight: 700,
                    lineHeight: "18px",
                  },
                },
              },
            },
          },

          "& .actions": {
            display: "flex",
            justifyContent: "flex-start",
            marginTop: "auto",
            padding: "20px 0",
            width: "100%",

            "& .MuiButton-root": {
              borderRadius: "8px",
              boxShadow: "none",
              height: "48px",
              justifyContent: "center",
              margin: 0,
              textTransform: "capitalize",

              "&.MuiButton-contained": {
                background: "#47C055",
                color: "#ffffff",
              },
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
