import React, { Fragment, useEffect, useState } from "react";
import {
  Button,
  Link,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";

import { UIButton } from "../../../../../Components/exports";
import { useToastMessage } from "../../../../../contexts";

import SubscriptionAPI from "../../../../../api/Subscription/index";

import ModifyDrawer from "../../ModifyDrawer";
import AddServiceDrawer from "../../AddServiceDrawer";
import UpdateDrawer from "../../UpdateDrawer";
import PaymentDrawer from "../../PaymentDrawer";

const Details = ({ businessId }) => {
  const [doModify, toggleModify] = useState(false);
  const [addService, toggleAddService] = useState(false);
  const [update, toggleUpdate] = useState(false);
  const [paid, togglePayment] = useState(false);

  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  const [countChanges, setCountChanges] = useState(0);
  const [busiSubscription, setBusiSubscription] = useState([]);
  const [defaultOrders, setDefaultOrders] = useState([]);
  const [updatedOrders, setUpdatedOrders] = useState([]);

  const [orderIndex, setOrderIndex] = useState();

  const loadSubscripion = async () => {
    const res = await SubscriptionAPI.getBusiSubscription(businessId);
    if (res.error) {
      setErrorMessage(res.error);
    } else {
      setBusiSubscription(res.data);
      setDefaultOrders(res.data.orders);
      setUpdatedOrders(res.data.orders);
    }
  };

  useEffect(() => {
    loadSubscripion();
  }, []);

  const formatAmount = (amount) => {
    let amt = Math.round(amount);
    return amt
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  const formatTwoPlace = (amount) => {
    return amount
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  const PlanItem = ({
    orderKey,
    plan_id,
    plan_category,
    particulars,
    short_desc,
    qty,
    amount,
  }) => {
    const [editPlanItem, toggleEditPlan] = useState(false);

    const setNewPlan = async (detail) => {
      let default_orders = [...defaultOrders];
      let default_element = { ...default_orders[orderIndex] };
      let defaultPlanId = parseInt(default_element.plan_id);

      // updated plan
      let temp_state = [...updatedOrders];
      let temp_element = { ...temp_state[orderIndex] };
      let oldAmount = temp_element.amount;
      let oldPlanId = parseInt(temp_element.plan_id);

      temp_element.amount = detail.amount;
      temp_element.plan_id = detail.planId;
      temp_state[orderIndex] = temp_element;
      setUpdatedOrders(temp_state);
      const updatedAmount =
        busiSubscription.totalAmount - oldAmount + detail.amount;
      setBusiSubscription({ ...busiSubscription, totalAmount: updatedAmount });
      if (oldPlanId != parseInt(detail.planId)) {
        setCountChanges(countChanges + 1);
      }
    };

    return (
      <Fragment>
        <TableRow>
          <TableCell align="left">
            <div className="particular">
              <p>
                {particulars} - {short_desc}
              </p>
              {doModify && (
                <div className="actions">
                  <Link
                    component="button"
                    underline="hover"
                    onClick={() => {
                      toggleEditPlan(true);
                      setOrderIndex(orderKey);
                    }}
                  >
                    {"Edit"}
                  </Link>
                  <Link component="button" underline="hover">
                    {"Cancel"}
                  </Link>
                </div>
              )}
            </div>
          </TableCell>
          <TableCell align="center">{qty}</TableCell>
          <TableCell align="right">{` ${formatTwoPlace(amount)}`}</TableCell>
          <TableCell align="right">{` ${formatTwoPlace(
            amount * qty
          )}`}</TableCell>
        </TableRow>
        <ModifyDrawer
          open={editPlanItem}
          closeDrawer={() => toggleEditPlan(false)}
          {...{
            orderKey,
            plan_id,
            plan_category,
            particulars,
            short_desc,
            qty,
            amount,
          }}
          setNewPlan={(detail) => setNewPlan(detail)}
        />
      </Fragment>
    );
  };

  return (
    <div className="card-group">
      <div className="card-header">
        <Link
          className="card-title faded"
          href="/all-payments/subscriptions/"
          underline="none"
        >
          {"Subscriptions"}
        </Link>
        <span className="card-title faded">{" / "}</span>
        <span className="card-title">{busiSubscription.business_name}</span>
      </div>
      <div className="card-content">
        <div className="detail-header">
          <div className="heading">
            <h4>{busiSubscription.business_name}</h4>
          </div>
          <div className="activation-date">
            <strong>{"Activated On"}</strong>
            <small>{busiSubscription.renewalDate}</small>
          </div>
        </div>
        {doModify && (
          <div className="header-action">
            <Button variant="contained" onClick={() => toggleAddService(true)}>
              {"Add Service"}
            </Button>
          </div>
        )}
        <TableContainer>
          <Table aria-label="Subscriptions" className="transactions-detail">
            <TableHead>
              <TableRow>
                <TableCell align="left">{"Particulars"}</TableCell>
                <TableCell align="center" width={30}>
                  {"Unit"}
                </TableCell>
                <TableCell align="right" width={90}>
                  {"Amount"}
                </TableCell>
                <TableCell align="right" width={90}>
                  {"Amount"}
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {updatedOrders &&
                updatedOrders.map((order, index) => (
                  <PlanItem key={index} {...order} />
                ))}
              <TableRow sx={{ borderTop: 1, borderColor: "#24272C1A" }}>
                <TableCell
                  colSpan={3}
                  sx={{ border: 0, py: "10px", px: "5px" }}
                >
                  <strong>{"Total"}</strong>
                </TableCell>
                <TableCell
                  align="right"
                  sx={{ border: 0, py: "10px", px: "5px" }}
                >
                  <strong>{` ${formatAmount(
                    busiSubscription.totalAmount
                  )}`}</strong>
                </TableCell>
              </TableRow>
              <TableRow sx={{ borderBottom: 1, borderColor: "#24272C1A" }}>
                <TableCell
                  colSpan={3}
                  sx={{ border: 0, py: "10px", px: "5px" }}
                >
                  <span>{`Taxes`}</span>
                </TableCell>
                <TableCell
                  align="right"
                  sx={{ border: 0, py: "10px", px: "5px" }}
                >
                  <span>{` ${
                    formatTwoPlace(busiSubscription.gst_rate / 100) *
                    busiSubscription.totalAmount
                  }`}</span>
                </TableCell>
              </TableRow>
              <TableRow>
                <TableCell
                  colSpan={3}
                  sx={{ border: 0, py: "10px", px: "5px" }}
                >
                  <strong>{"Total Payable"}</strong>
                </TableCell>
                <TableCell
                  align="right"
                  sx={{ border: 0, py: "10px", px: "5px" }}
                >
                  <strong>{` ${formatTwoPlace(
                    (busiSubscription.gst_rate / 100) *
                      busiSubscription.totalAmount +
                      busiSubscription.totalAmount
                  )}`}</strong>
                </TableCell>
              </TableRow>

              {/* <TableRow sx={{ borderTop: 1, borderColor: "#24272C1A" }}>
                  <TableCell colSpan={3} sx={{ border: 0, py: "10px", px: "5px" }}>
                    <strong>{"Total"}</strong>
                  </TableCell>
                  <TableCell align='right' sx={{ border: 0, py: "10px", px: "5px" }}>
                    <strong>{` ${formatAmount(6000)}`}</strong>
                  </TableCell>
                </TableRow>
                <TableRow sx={{ borderBottom: 1, borderColor: "#24272C1A" }}>
                  <TableCell colSpan={3} sx={{ border: 0, py: "10px", px: "5px" }}>
                    <span>{`Taxes`}</span>
                  </TableCell>
                  <TableCell align='right' sx={{ border: 0, py: "10px", px: "5px" }}>
                    <span>{` ${formatAmount(1080)}`}</span>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell colSpan={3} sx={{ border: 0, py: "10px", px: "5px" }}>
                    <strong>{"Total Payable"}</strong>
                  </TableCell>
                  <TableCell align='right' sx={{ border: 0, py: "10px", px: "5px" }}>
                    <strong>{` ${formatAmount(7080)}`}</strong>
                  </TableCell>
                </TableRow> */}
            </TableBody>
          </Table>
        </TableContainer>
        <div className="detail-footer">
          {!doModify && (
            <Button variant="contained" className="btn-sub">
              {"Cancel"}
            </Button>
          )}
          {!doModify && (
            <Button
              variant="contained"
              className="btn-main"
              onClick={() => toggleModify(true)}
            >
              {"Modify"}
            </Button>
          )}
          {doModify && (
            <Button
              variant="contained"
              className="btn-discard"
              onClick={() => toggleModify(false)}
            >
              {"Discard"}
            </Button>
          )}
          {doModify && (
            <UIButton onClick={() => toggleUpdate(true)}>
              {"Apply Changes (1)"}
            </UIButton>
          )}
        </div>
      </div>
      <AddServiceDrawer
        open={addService}
        closeDrawer={() => toggleAddService(false)}
      />
      <UpdateDrawer
        open={update}
        closeDrawer={() => toggleUpdate(false)}
        onPaymentSuccess={() => togglePayment(true)}
      />
      <PaymentDrawer open={paid} closeDrawer={() => togglePayment(false)} />
    </div>
  );
};

export default Details;
