import React, { Fragment } from "react";
import { Hidden } from "@material-ui/core";
import { useParams } from "react-router-dom";

import MobileSubscriptions from "../../../Container/AllPayments/Subscriptions/Mobile";
import DesktopSubscriptions from "../../../Container/AllPayments/Subscriptions/Desktop";

import Layout from "../Layout";

interface ISubscriptionsPage {}

const SubscriptionsPage: React.FunctionComponent<ISubscriptionsPage> = () => {
  const { businessId } = useParams<any>();

  return (
    <Fragment>
      <Hidden smUp implementation="js">
        <MobileSubscriptions businessId={businessId} />
      </Hidden>
      <Hidden xsDown implementation="js" only="xs">
        <Layout useFluid>
          <DesktopSubscriptions businessId={businessId} />
        </Layout>
      </Hidden>
    </Fragment>
  );
};

export default SubscriptionsPage;
