import React, { useState, useEffect } from "react";
import { Divider, Drawer, IconButton, MenuItem } from "@mui/material";

import { CustomTextFieldFilled } from "../../../../Components/UI/CustomTextField/CustomTextField";
import CustomIcon from "../../../../shared-components/CustomIcon/CustomIcon";
import UIButton from "../../../../Components/UI/UIButton/UIButton";

import useStyles from "./styles/muiStyles";
import { useToastMessage } from "../../../../contexts";
import { getCategoryPlan } from "../../../../api/Plan";
import { useSubscription } from "../../../../contexts/SubscriptionProvider";

const AddServiceDrawer = ({
  open,
  closeDrawer,
  orderKey,
  plan_id,
  particulars,
  short_desc,
  qty,
  amount,
  setNewPlan,
}) => {
  const classes = useStyles();
  const { setErrorMessage } = useToastMessage();
  const { defaultOrders } = useSubscription();

  const category =
    defaultOrders[0].plan_category === "Engage" ? "Curate" : "Engage";

  const [plans, setPlans] = useState([]);
  const [selectedPlan, setSelectedPlan] = useState("");

  const [planId, setPlanId] = useState(plan_id);

  const plan_type = "Monthly";

  const getPlan = async () => {
    const res = await getCategoryPlan(category, plan_type);
    if (res.error) {
      setErrorMessage(res.error);
    } else {
      setPlans(res.data);
    }
  };

  useEffect(() => {
    getPlan();
  }, []);

  useEffect(() => {
    const selectedP = plans.filter((plan) => plan.planId === parseInt(planId));
    setSelectedPlan(selectedP[0]);
  }, [plans, planId]);

  const inputChangedHandler = (e) => {
    setPlanId(e.target.value);
  };

  const updateSubscription = async () => {
    setNewPlan(selectedPlan);
  };

  const formatTwoPlace = (amount) => {
    return amount
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  return (
    <Drawer
      anchor={"right"}
      open={open}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") &&
        closeDrawer()
      }
      className={classes["modify-subscriptions"]}
    >
      <div className="wrapper">
        <div className="header">
          <IconButton
            aria-label="Back"
            className="back-btn"
            style={{ color: "transparent", padding: 0 }}
            onClick={() => closeDrawer()}
            disableFocusRipple={true}
            disableRipple={true}
          >
            <CustomIcon.BackBtnIcon />
          </IconButton>
          <h1 className="title">{"Modify Subscriptions"}</h1>
        </div>
        <div className="content">
          <div className="main">
            <div className="info">
              <h6 className="info-heading">{"Choose Category"}</h6>
            </div>

            {false && (
              <div className="field">
                <CustomTextFieldFilled
                  select
                  label="Plan Category"
                  placeholder="Plan Category"
                  variant="filled"
                  type="select"
                  inputProps={{ "aria-label": "Without label" }}
                  fullWidth
                  value={planId}
                  onChange={(e) => inputChangedHandler(e)}
                >
                  {[
                    { name: "Engage", planId: 1 },
                    { name: "Curate", planId: 2 },
                  ].map((plan, index) => {
                    return (
                      <MenuItem key={index} value={plan.planId}>
                        {plan.name}
                      </MenuItem>
                    );
                  })}
                </CustomTextFieldFilled>
              </div>
            )}

            <div className="field">
              <CustomTextFieldFilled
                select
                label="Select Plan"
                placeholder="Select Plan"
                variant="filled"
                type="select"
                inputProps={{ "aria-label": "Without label" }}
                fullWidth
                value={planId}
                onChange={(e) => inputChangedHandler(e)}
              >
                {plans &&
                  plans.map((plan, index) => {
                    return (
                      <MenuItem key={index} value={plan.planId}>
                        {plan.name}
                      </MenuItem>
                    );
                  })}
              </CustomTextFieldFilled>
            </div>
            <div className="plan-details">
              <h4 className="heading">{"Plan Details"}</h4>
              <div className="plan">
                {selectedPlan && (
                  <>
                    <h5 className="name">
                      {selectedPlan.category} - {selectedPlan.name}
                    </h5>
                    <p className="price">
                      {selectedPlan.currency === "INR" ? "" : "$"}{" "}
                      {formatTwoPlace(selectedPlan.amount)}/
                      {selectedPlan.plan_type}
                    </p>
                  </>
                )}
              </div>
              <Divider variant="fullWidth" style={{ color: "#24272c1a" }} />
              <ul className={`features`}>
                {selectedPlan &&
                  selectedPlan.plan_detail
                    .split("\n")
                    .map((feature, index) => <li key={index}>{feature}</li>)}
              </ul>
            </div>
            {selectedPlan && (
              <div className="actions">
                <UIButton
                  type="button"
                  variant="contained"
                  onClick={() => {
                    updateSubscription();
                    closeDrawer();
                  }}
                >
                  {"Add to Subscription"}
                </UIButton>
              </div>
            )}
          </div>
        </div>
      </div>
    </Drawer>
  );
};

export default AddServiceDrawer;
