import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "mobile-settings": {
    overflow: "hidden",
    width: "100%",

    "& .header": {
      alignItems: "center",
      borderBottom: "1px solid #24272c1a",
      display: "flex",
      gap: "20px",
      height: "70px",
      padding: "20px",
      width: "100%",

      "& > a": {
        alignItems: "center",
        display: "flex",
        height: "100%",
      },

      "& .title": {
        color: "#24272c",
        fontWeight: 500,
        fontSize: "24px",
        lineHeight: "28px",
      },
    },

    "& .content": {
      display: "flex",
      flexDirection: "column",
      rowGap: "36px",
      width: "100%",

      "& .card-group": {
        "& .card-header": {
          borderBottom: "1px solid #24272c1a",
          padding: "24px 34px",
          marginBottom: "8px",

          "& .card-title": {
            color: "#24272C",
            fontSize: "16px",
            fontWeight: 500,
            lineHeight: "19px",

            "&.faded": {
              color: "#24272C80",
            },
          },
        },

        "& .card-content": {
          padding: "24px 34px",

          "& .detail-header": {
            alignItems: "center",
            columnGap: "10px",
            display: "flex",
            justifyContent: "space-between",
            marginBottom: "40px",

            "& .heading": {
              flexGrow: 1,

              "& h4": {
                color: "#24272C",
                fontSize: "18px",
                fontWeight: 500,
                lineHeight: "21px",
              },
            },

            "& .activation-date": {
              alignItems: "baseline",
              display: "flex",
              columnGap: "6px",
              flexShrink: 1,

              "& strong": {
                color: "#24272CB2",
                fontSize: "15px",
                fontWeight: 800,
                lineHeight: "18px",
              },

              "& small": {
                color: "#24272CB3",
                fontSize: "13px",
                fontWeight: 500,
                lineHeight: "15px",
                textAlign: "right",
              },
            },
          },

          "& .header-action": {
            display: "flex",
            justifyContent: "end",
            marginBottom: "22px",

            "& .MuiButton-root": {
              backgroundColor: "#12B0EE",
              color: "#ffffff",
              borderRadius: "8px",
              boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
              height: "39px",
              margin: 0,
              textTransform: "capitalize",
            },
          },

          "& .transactions-detail": {
            "& .MuiTableHead-root": {
              "& .MuiTableRow-root": {
                borderBottom: "1px solid #24272C1A",
              },

              "& .MuiTableCell-root": {
                color: "#24272cb3",
                borderBottom: "none",
                fontWeight: 700,
                padding: "0px 8px 4px",
                whiteSpace: "nowrap",
              },
            },

            "& .MuiTableBody-root": {
              "& .MuiTableRow-root": {
                color: "#24272cb3",

                "& .MuiTableCell-root": {
                  color: "#24272cb3",
                  fontSize: "12px",
                  fontWeight: 400,
                  lineHeight: "14px",
                  borderBottom: "none",
                  verticalAlign: "top",
                  whiteSpace: "nowrap",

                  "&:not(.loading)": {
                    padding: "12px 8px 7px",
                  },

                  "& .particular": {
                    display: "flex",
                    flexDirection: "column",
                    gap: "10px",
                    justifyContent: "space-between",

                    "& p": {
                      maxWidth: "calc(100% - 100px)",
                      whiteSpace: "break-spaces",
                    },

                    "& .actions": {
                      display: "flex",
                      columnGap: "16px",
                      flexShrink: 1,

                      "& .MuiLink-root": {
                        color: "#12B0EE",
                        fontSize: "12px",
                        fontWeight: 400,
                        lineHeight: "14px",
                      },
                    },
                  },

                  "& strong": {
                    fontSize: "15px",
                    fontWeight: 800,
                    lineHeight: "18px",
                  },

                  "& span": {
                    fontSize: "14px",
                    lineHeight: "16px",
                  },
                },
              },
            },
          },

          "& .detail-footer": {
            alignItems: "flex-end",
            display: "flex",
            flexDirection: "column",
            gap: "12px",
            justifyContent: "end",
            marginTop: "60px",

            "& .MuiButton-root": {
              color: "#ffffff",
              borderRadius: "8px",
              boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
              height: "39px",
              margin: 0,
              textTransform: "capitalize",
            },

            "& .btn-main": {
              backgroundColor: "#12B0EE",
              width: "100px",
            },

            "& .btn-sub": {
              backgroundColor: "#E02020",
              width: "100px",
            },

            "& .btn-discard": {
              backgroundColor: "#bebebe",
              width: "110px",
            },
          },
        },

        "& .transactions": {
          "& .MuiTableHead-root": {
            "& .MuiTableRow-root": {
              borderBottom: "1px solid #24272C1A",
            },

            "& .MuiTableCell-root": {
              color: "#24272cb3",
              borderBottom: "none",
              fontWeight: 700,
              padding: "0px 8px 4px",
              whiteSpace: "nowrap",
            },
          },

          "& .MuiTableBody-root": {
            "& .MuiTableRow-root": {
              borderBottom: "1px solid #24272C1A",
              color: "#24272cb3",

              "&.MuiTableRow-hover": {
                "&:hover": {
                  backgroundColor: "#EDEFF080",
                },
              },

              "& .MuiTableCell-root": {
                borderBottom: "none",
                fontWeight: 400,
                height: "95px",
                verticalAlign: "top",
                whiteSpace: "nowrap",

                "& p": {
                  fontSize: "13px",
                  lineHeight: "15px",

                  "& small": {
                    display: "block",
                    fontSize: "11px",
                    lineHeight: "13px",
                  },
                },

                "&:not(.loading)": {
                  padding: "12px 0 7px",
                },

                "& .MuiLink-root": {
                  color: "#12B0EE",
                  display: "inline-flex",
                  fontSize: "13px",
                  fontWeight: 500,
                  lineHeight: "15px",
                },

                "&:last-child": {
                  "& .MuiSkeleton-root": {
                    backgroundColor: "#32C5FF80",
                  },
                },

                "& .MuiSkeleton-root": {
                  backgroundColor: "#3B414A",
                  margin: "0 auto",
                },
              },
            },
          },

          "& .MuiTableCell-root": {
            fontSize: "14px",
            lineHeight: "16px",

            "& .actions": {
              display: "flex",
              columnGap: "10px",
              justifyContent: "center",
            },
          },

          "& .report-download": {
            alignItems: "center",
            color: "#32C5FF",
            display: "inline-flex",
            fontSize: "14px",
            fontWeight: 500,
            lineHeight: "16px",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    "stacked-cards": {
      display: "flex",
      flexDirection: "column",
      paddingBottom: "50px",
      paddingTop: "50px",
      maxWidth: "650px",
      rowGap: "36px",
      width: "100%",

      "& .card-group": {
        backgroundColor: "#ffffff",
        borderRadius: "10px",
        boxShadow: "0px 0px 20px 1px rgba(0, 0, 0, 0.1)",

        "& .card-header": {
          borderBottom: "1px solid #24272c1a",
          padding: "24px 34px",
          marginBottom: "8px",

          "& .card-title": {
            color: "#24272C",
            fontSize: "18px",
            fontWeight: 500,
            lineHeight: "21px",

            "&.faded": {
              color: "#24272C80",
            },
          },
        },

        "& .card-content": {
          padding: "24px 34px",

          "& .detail-header": {
            alignItems: "center",
            columnGap: "10px",
            display: "flex",
            justifyContent: "space-between",
            marginBottom: "40px",

            "& .heading": {
              flexGrow: 1,

              "& h4": {
                color: "#24272C",
                fontSize: "18px",
                fontWeight: 500,
                lineHeight: "21px",
              },
            },

            "& .activation-date": {
              alignItems: "baseline",
              display: "flex",
              columnGap: "6px",
              flexShrink: 1,

              "& strong": {
                color: "#24272CB2",
                fontSize: "15px",
                fontWeight: 800,
                lineHeight: "18px",
              },

              "& small": {
                color: "#24272CB3",
                fontSize: "13px",
                fontWeight: 500,
                lineHeight: "15px",
                textAlign: "right",
              },
            },
          },

          "& .header-action": {
            display: "flex",
            justifyContent: "end",
            marginBottom: "22px",

            "& .MuiButton-root": {
              backgroundColor: "#12B0EE",
              color: "#ffffff",
              borderRadius: "8px",
              boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
              height: "39px",
              margin: 0,
              textTransform: "capitalize",
            },
          },

          "& .transactions-detail": {
            "& .MuiTableHead-root": {
              "& .MuiTableRow-root": {
                borderBottom: "1px solid #24272C1A",
              },

              "& .MuiTableCell-root": {
                color: "#24272cb3",
                borderBottom: "none",
                fontWeight: 700,
                padding: "0px 8px 4px",
                whiteSpace: "nowrap",
              },
            },

            "& .MuiTableBody-root": {
              "& .MuiTableRow-root": {
                color: "#24272cb3",

                "& .MuiTableCell-root": {
                  color: "#24272cb3",
                  fontSize: "12px",
                  fontWeight: 400,
                  lineHeight: "14px",
                  borderBottom: "none",
                  verticalAlign: "top",
                  whiteSpace: "nowrap",

                  "&:not(.loading)": {
                    padding: "12px 8px 7px",
                  },

                  "& .particular": {
                    display: "flex",
                    flexWrap: "wrap",
                    gap: "10px",
                    justifyContent: "space-between",

                    "& .actions": {
                      display: "flex",
                      columnGap: "16px",
                      flexShrink: 1,

                      "& .MuiLink-root": {
                        color: "#12B0EE",
                        fontSize: "12px",
                        fontWeight: 400,
                        lineHeight: "14px",
                      },
                    },
                  },

                  "& strong": {
                    fontSize: "15px",
                    fontWeight: 800,
                    lineHeight: "18px",
                  },

                  "& span": {
                    fontSize: "14px",
                    lineHeight: "16px",
                  },
                },
              },
            },
          },

          "& .detail-footer": {
            columnGap: "12px",
            display: "flex",
            justifyContent: "end",
            marginTop: "60px",

            "& .MuiButton-root": {
              color: "#ffffff",
              borderRadius: "8px",
              boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
              height: "39px",
              margin: 0,
              textTransform: "capitalize",
            },

            "& .btn-main": {
              backgroundColor: "#12B0EE",
              width: "100px",
            },

            "& .btn-sub": {
              backgroundColor: "#E02020",
              width: "100px",
            },

            "& .btn-discard": {
              backgroundColor: "#bebebe",
              width: "110px",
            },
          },
        },

        "& .transactions": {
          "& .MuiTableHead-root": {
            "& .MuiTableRow-root": {
              borderBottom: "1px solid #24272C1A",
            },

            "& .MuiTableCell-root": {
              color: "#24272cb3",
              borderBottom: "none",
              fontWeight: 700,
              padding: "0px 8px 4px",
            },
          },

          "& .MuiTableBody-root": {
            "& .MuiTableRow-root": {
              borderBottom: "1px solid #24272C1A",
              color: "#24272cb3",

              "&.MuiTableRow-hover": {
                "&:hover": {
                  backgroundColor: "#EDEFF080",
                },
              },

              "& .MuiTableCell-root": {
                borderBottom: "none",
                fontWeight: 400,
                height: "95px",
                verticalAlign: "top",

                "& p": {
                  fontSize: "13px",
                  lineHeight: "15px",

                  "& small": {
                    display: "block",
                    fontSize: "11px",
                    lineHeight: "13px",

                    "& .MuiLink-root": {
                      color: "#12B0EE",
                      display: "inline-flex",
                      fontSize: "10px",
                      lineHeight: "12px",
                    },
                  },
                },

                "&:not(.loading)": {
                  padding: "12px 8px 7px",
                },

                "& .MuiLink-root": {
                  color: "#12B0EE",
                  display: "inline-flex",
                  fontSize: "13px",
                  fontWeight: 500,
                  lineHeight: "15px",
                },

                "&:last-child": {
                  "& .MuiSkeleton-root": {
                    backgroundColor: "#32C5FF80",
                  },
                },

                "& .MuiSkeleton-root": {
                  backgroundColor: "#3B414A",
                  margin: "0 auto",
                },
              },
            },
          },

          "& .MuiTableCell-root": {
            fontSize: "14px",
            lineHeight: "16px",

            "& .actions": {
              display: "flex",
              columnGap: "10px",
              justifyContent: "center",
            },
          },

          "& .report-download": {
            alignItems: "center",
            color: "#32C5FF",
            display: "inline-flex",
            fontSize: "14px",
            fontWeight: 500,
            lineHeight: "16px",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("md")]: {
    "stacked-cards": {
      "& .card-group": {
        "& .status-blocks": {
          "& .block": {
            minWidth: "175px",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
