import React, { useState, useEffect } from "react";
import { Button, Drawer, IconButton } from "@mui/material";
import moment from "moment";

import CustomIcon from "../../../../shared-components/CustomIcon/CustomIcon";
import Check from "../../../../assets/icons/successLogo.svg";
import Failed from "../../../../assets/icons/failedLogo.svg";

import useStyles from "./styles/muiStyles";
import { useToastMessage } from "../../../../contexts";
import { useWaitingLoader } from "../../../../contexts/waitingLoaderContext";
import { createInvoicePdf, downloadInvoicePdf } from "../../../../api/Invoice";

import { useSubscription } from "../../../../contexts/SubscriptionProvider";

const PaymentDrawer = ({ businessId, open, closeDrawer }) => {
  const classes = useStyles();
  const { nextMonthAmount, invoiceDetail, paymentStatus } = useSubscription();
  const { openLoader, closeLoader } = useWaitingLoader();
  const { setSuccessMessage, setErrorMessage } = useToastMessage();

  const status = paymentStatus;

  console.log("invoiceDetail", invoiceDetail);

  let now = new Date();
  let current = new Date(now.getFullYear(), now.getMonth() + 1, 1);
  let nextMonthDate = moment(current).format("DD/MMM/YYYY");

  const formatTwoPlace = (amount) => {
    return amount
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  const backToSubscribe = async () => {
    window.location.href = "/all-payments/subscriptions";
  };

  const downloadInvoice = async (invoiceId) => {
    openLoader();
    const res = await createInvoicePdf({ businessId, invoiceId });
    if (res.error) {
      return setErrorMessage(res.error);
    } else {
      downloadInvoicePdf(res)
        .then((response) => response.blob())
        .then((blob) => {
          // Create blob link to download
          const url = window.URL.createObjectURL(new Blob([blob]));
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", `${res}.pdf`);
          // Append to html link element page
          document.body.appendChild(link);

          // Start download
          link.click();

          // Clean up and remove the link
          link.parentNode.removeChild(link);
          closeLoader();
          setSuccessMessage("Invoice Downloaded");
        })
        .catch((err) => {
          setErrorMessage(err.Error);
          closeLoader();
        });
    }
  };

  return (
    <Drawer
      anchor={"right"}
      open={open}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") &&
        closeDrawer()
      }
      className={classes["modify-subscriptions"]}
    >
      <div className="wrapper">
        <div className="header">
          <IconButton
            aria-label="Back"
            className="back-btn"
            style={{ color: "transparent", padding: 0 }}
            onClick={() => {
              closeDrawer();
              backToSubscribe();
            }}
            disableFocusRipple={true}
            disableRipple={true}
          >
            <CustomIcon.BackBtnIcon />
          </IconButton>
          <h1 className="title">{"Payment Summary"}</h1>
        </div>
        <div className="content">
          <div className="main">
            <div className="payment-wrapper">
              <div className="status">
                <div className={status ? "success" : "failed"}>
                  <img
                    src={status ? Check : Failed}
                    alt={`${status ? "success" : "failed"} badge`}
                    className="badge"
                  />
                </div>
                <h3 className="label">
                  {status ? "Payment Successful!" : "Payment Failed!"}
                </h3>
                {status && (
                  <h5 className="amount">{`Received   ${formatTwoPlace(
                    invoiceDetail.payable_amount
                  )}`}</h5>
                )}
              </div>
              <div className="next-payment">
                <h4 className="title">{"Next Recurring:"}</h4>
                <div className="info">
                  <h5 className="label">{"Date"}</h5>
                  <h5 className="value">{nextMonthDate}</h5>
                </div>
                <div className="info">
                  <h5 className="label">{"Amount"}</h5>
                  <h5 className="value">{` ${formatTwoPlace(
                    nextMonthAmount
                  )}`}</h5>
                </div>
              </div>
            </div>
            <div className="actions">
              <Button
                type="button"
                variant="contained"
                className="btn-close"
                onClick={backToSubscribe}
              >
                {"Close"}
              </Button>
              {invoiceDetail && (
                <Button
                  type="button"
                  variant="contained"
                  onClick={() => downloadInvoice(invoiceDetail._id)}
                >
                  {"Download Invoice"}
                </Button>
              )}
            </div>
          </div>
        </div>
      </div>
    </Drawer>
  );
};

export default PaymentDrawer;
